import React from "react";

import Box from "@material-ui/core/Box";
import Button from "@material-ui/core/Button";

import { makeStyles } from "@material-ui/styles";

import DropDownSubmittableTextField from "./DropDownSubmittableTextField.js"

const useStyles = makeStyles((theme) => ({
  box: {
    display: "flex",
    justifyContent: "center",
    // margin: theme.spacing(1),
  },
  dropDown: {
    // color: theme.palette.primary.contrastText,
  },
  dropDownContainer: {
    // backgroundColor: theme.palette.primary.main,
    borderColor: theme.palette.primary.main,
    borderRadius: 5,
    borderStyle: "solid",
    borderWidth: 2,
    height: 38,
    width: 150,
    marginTop: theme.spacing(1),
  },
  flankButton: {
    margin: theme.spacing(1),
  },
  overrideTextFieldInputInput: {
    // color: theme.palette.primary.contrastText,
    backgroundColor: "white",
    ...theme.typography.dataText,
  },
  root: {
    height: "100%",
    backgroundColor: "white",
    width: "100%",
  },
}));

const ButtonFlankedDropdown = (props) => {
  const {
    value,
    dropdownOptions = [],
    leftBtnDom,
    rightBtnDom,
    leftBtnProps,
    rightBtnProps,
    // onLeftClick,
    // onRightClick,
    onChange,
    onSelect,
    onSubmit,
  } = props;

  const classes = useStyles();

  const handleChangeValue = (val) => {
    if(onChange){
      onChange(val)
    }
  }

  const handleSelectValue = ({index, value}) => {
    if(onSelect){
      onSelect(index, value);
    }
  };

  const handleSubmitValue = (val) => {
    if(onSelect){
      onSubmit(val);
    }
  };

  return <Box className={classes.box}>
    <Button
      size="large"
      color="primary"
      variant="contained"
      className={classes.flankButton}
      {...leftBtnProps}
    >
      {leftBtnDom}
    </Button>
    <DropDownSubmittableTextField

      value={value}
      menuItems={dropdownOptions}
      onChange={handleChangeValue}
      onSelect={handleSelectValue}
      onSubmit={(event) => handleSubmitValue(event.target.value)}
      buttonProps={{
        classes: {
          root: classes.dropDown
        }
      }}
      containerProps={{
        className: classes.dropDownContainer
      }}
      textFieldProps={{
        InputProps: {
          disableUnderline: true,
          classes: {
            input: classes.overrideTextFieldInputInput,
            root: classes.root,
          }
        }
      }}
    />
    <Button
      size="large"
      color="primary"
      variant="contained"
      className={classes.flankButton}
      {...rightBtnProps}
    >
      {rightBtnDom}
    </Button>
  </Box>
};
export default ButtonFlankedDropdown;
