import React, {useState, useRef} from 'react';

import Button from '@material-ui/core/Button';
import Tooltip from '@material-ui/core/Tooltip';
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';
import HelpIcon from '@material-ui/icons/Help';
import MenuIcon from '@material-ui/icons/Menu';
import { makeStyles } from '@material-ui/styles';

import useTranslation from '../hooks/useTranslation';

import HomeAllButton from '../containers/HomeAllButton';
import JogButtons from '../containers/JogButtons.js'
import JogMenu from '../containers/JogMenu.js'
import JogSpinners from '../containers/JogSpinners';
import Panel from './Panel';

const useStyles = makeStyles((theme) => ({
  extras: {
    display: "flex",
    marginBottom: theme.spacing(1)
  },
  homeAllButton: {
    marginLeft: theme.spacing(1),
  },
  label: {
    ...theme.typography.statusItem,
    fontSize: 14,
    marginLeft: theme.spacing(1),
  },
  menuSpan: {
    width: "100%",
  },
  select: {
    height: "30px"
  },
  tabButton: {
    display: "flex",
    // marginLeft: "auto",
    float: "right",
  }
}));


const Jog = (props) => {
  const { 
    display,
    onSetDisplay,
  } = props;
  const { t } = useTranslation();

  const classes = useStyles();

  const handleChangeDisplay = (event) => {
    onSetDisplay(event.target.value);
  };


  const [ menuOpen, setMenuOpen ] = useState(false);
  const buttonRef = useRef(null);
  const toggleMenuOpen = (e) => {
    setMenuOpen(!menuOpen);
  };
  const handleClose = () => {
    setMenuOpen(false);
  };

  return <Panel label={t("jog")}>
    <div className={classes.extras}>  
      <Select variant="outlined" value={display} onChange={handleChangeDisplay} className={classes.select}>
        <MenuItem value="position"><Tooltip title={t('tooltip_OffsetPosition')}><span className={classes.menuSpan}>{ t("position") }</span></Tooltip></MenuItem>
        <MenuItem value="actualPosition"><Tooltip title={t('tooltip_ActualPosition')}><span className={classes.menuSpan}>{ t("actualPosition") }</span></Tooltip></MenuItem>
        {/* {G5X_SYSTEMS.map((sys, idx) => {
          return(
            <MenuItem key={sys} value={sys}><Tooltip title={t('tooltip_G5xPosition')}><span>{ `${t("position")}: ${t("coordinateSystem")} ${idx + 1}`}</span></Tooltip></MenuItem>
          )
        })} */}
      </Select>
      <HomeAllButton classes={{ button: classes.homeAllButton }}/>
      <span style={{display:'flex', float: 'right', marginLeft: 'auto'}}><Tooltip placement="top"  title={<span>{t("tooltip_JogPanel")}</span>}><HelpIcon/></Tooltip></span>
      <Button classes={ { root: classes.tabButton } } onClick={toggleMenuOpen} ref={buttonRef}><MenuIcon/></Button>
      <JogMenu
        open={menuOpen}
        onClose={handleClose}
        anchor={buttonRef.current}
      />
    </div>

    <JogSpinners/>

    <JogButtons/>

  </Panel>;
};
export default Jog;
