export const MODULE_ID = "@@calib";
export const LOCALSTORAGE_KEY = "calib";

export const ACTIVE = "active";
export const INACTIVE = "inactive";
export const UNKNOWN = "unknown";

export const MODE_CALIB = 'MODE_CALIB';
export const MODE_VERIFY = 'MODE_VERIFY';

//These STATE_ constants are used to set value of CalibProcess.commandedState and CalibProcess.actualState 
export const STATE_INIT = "INIT" //If configuration does not begin process immediately on startup. Currently not used
export const STATE_IDLE = "IDLE" //In between stages. Enter this state if commandedState is STATE_STEP upon completion of a stage
export const STATE_PAUSE = "PAUSE" //Mid-stage but paused
export const STATE_RUN = "RUN" //Automatically progression between stages
export const STATE_STEP = "STEP" //Idle after each stage completes
export const STATE_STOP = "STOP" //Terminate the process
export const STATE_ERROR = "ERROR" //Something has gone wrong, process should stop
export const STATE_FAIL = "FAIL" //The machine has failed a spec check, process should stop
export const STATE_COMPLETE = "COMPLETE" //The calibration process has completed successfully

export const CALIB_STAGES = [
  'ERASE_COMPENSATION', 
  'SETUP_CNC', 'SETUP_CMM',
  'PROBE_MACHINE_POS', 
  'SETUP_PART_CSY', 
  'PROBE_SPINDLE_POS',
  'CHARACTERIZE_X', 
  'CHARACTERIZE_Z', 
  'PROBE_TOP_PLANE',
  'PROBE_FIXTURE_BALL_POS',
  'CHARACTERIZE_Y', 
  'SETUP_CNC_CSY', 
  'CHARACTERIZE_A', 'CHARACTERIZE_B', 
  'CALC_CALIB',
  'WRITE_CALIB'
];
export const VERIFY_STAGES = [
  'RESTART_CNC', 
  'SETUP_CNC', 
  'SETUP_CMM',
  'SETUP_VERIFY',
  //'VERIFY_X', 'VERIFY_Y', 'VERIFY_Z', 
  // 'VERIFY_A_HOMING',
  'VERIFY_A', 
  // 'VERIFY_B_HOMING',
  'VERIFY_B',
  'CALC_VERIFY',
  'WRITE_VERIFY'
]
export const CALIB_STATUS = {stages: {verify: {}, calib: {}}, status: {}, begun: false, currentStage: null};
CALIB_STAGES.forEach(stage => CALIB_STATUS.stages.calib[stage] = {completed: false, error: false})
VERIFY_STAGES.forEach(stage => CALIB_STATUS.stages.verify[stage] = {completed: false, error: false})

