import React from 'react';
import Button from '@material-ui/core/Button';
import Tooltip from '@material-ui/core/Tooltip';
import useTranslation from '../hooks/useTranslation';
import Panel from './Panel';
import fileCache from '../modules/rockhopper/file-cache';

const FileSettings = (props) => {
  const { t } = useTranslation();

  const handleClearCache = () => {
    fileCache.clear();
  };

  return <Panel label={t("files")}>
    <Tooltip title={t("tooltip_ClearCache")}><Button variant="outlined" onClick={handleClearCache}>{ t("clearCache") }</Button></Tooltip>
  </Panel>
}
export default FileSettings;
