import React from 'react';

import Grid from '@material-ui/core/Grid';
import { useLocation } from "react-router-dom";

import Display from './Display';
import ConnectionDetails from '../containers/ConnectionDetails';
import KineticConnectionDetails from '../containers/KineticConnectionDetails';
import SoundConfig from '../containers/SoundConfig';
import Language from '../containers/Language';
import Units from '../containers/Units';
import KeyboardShortcutsConfig from '../containers/KeyboardShortcutsConfig';
import FileSettings from './FileSettings';
import { isMobile } from 'react-device-detect';

const ClientTab = (props) => {
  const location = useLocation();
  const showWebSocket = location.search.indexOf("websocket=1") !== -1;
  return (<Grid container>
    <Grid item xs={12} sm={6} md={6} lg={4}>
      <KeyboardShortcutsConfig/>
    </Grid>
    <Grid item xs={12} sm={6} md={6} lg={4}>
      <Units/>
      <SoundConfig/>
      <Language/>
      { isMobile ?  <Display/> : null }
      <FileSettings/>
    </Grid>
    <Grid item xs={12} md={12} lg={4}>
      { showWebSocket ?
      <><ConnectionDetails/><KineticConnectionDetails/></> : null }
    </Grid>
  </Grid>);
};

export default ClientTab;
