import { connect } from 'react-redux';
import MachineConfigExtras from '../components/MachineConfigExtras';
import rockhopperActions from '../modules/rockhopper/actions';
import { selectBoardRevision, selectToolProbeOnM6} from '../modules/rockhopper/selectors';

export default connect(
  (state, ownProps) => {
    return { 
      boardRevision: selectBoardRevision(state),
      toolProbeOnM6: selectToolProbeOnM6(state),
    }
  }, 
  (dispatch, ownProps) => ({ 
    onToggleMachineVersionV1: () => {
      dispatch(rockhopperActions.put.toggleV1V2revp());
    },
    onSetM6ToolProbe: (val) => {
      dispatch(rockhopperActions.put.setM6ToolProbe({data: [val]}));
    },
  })
)(MachineConfigExtras);
