import React, { useRef, useCallback } from 'react';
import Panel from './Panel';

import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import { makeStyles } from '@material-ui/styles';
import { useTranslation } from 'react-i18next';

const useStyles = makeStyles((theme) => ({
  buttonContainer: {
    textAlign: "right"
  },
  button: {
    marginLeft: theme.spacing(2),
  },
  gapUnder: {
    marginBottom: theme.spacing(2),
  },
  input: {
    paddingTop: theme.spacing(1),
    paddingBottom: theme.spacing(1)
  }
}));

const ConnectionDetails = (props) => {
  const {
    host,
    port,
    onUpdate
  } = props;
  const classes = useStyles();
  const { t } = useTranslation();

  const hostRef = useRef(null);
  const portRef = useRef(null);

  const handleReset = useCallback(() => {
    hostRef.current.value = host;
    portRef.current.value = port;
  }, [ host, port, hostRef, portRef ]);

  const handleUpdate = useCallback(() => {
    if(onUpdate) {
      onUpdate(hostRef.current.value,
               portRef.current.value);
    }
  }, [ onUpdate, hostRef, portRef ]);

  return <Panel label={"Kinetic Control"}>
    <form>
      <TextField autoComplete={"kinetic host"} classes={ { root: classes.gapUnder} } InputProps={ { classes: { input: classes.input } } } label={t("host")} defaultValue={host} variant="outlined" fullWidth inputRef={hostRef}/><br/>
      <TextField autoComplete={"kinetic port"} classes={ { root: classes.gapUnder } } InputProps={ { classes: { input: classes.input } } } label={t("port")} defaultValue={port} variant="outlined" fullWidth inputRef={portRef}/><br/>
      <div className={classes.buttonContainer}>
      <Button color="secondary" 
              variant="contained" 
              classes={ { root: classes.button } }
              onClick={handleReset}>{t("reset")}</Button>
      <Button color="primary" 
              variant="contained" 
              classes={ { root: classes.button } }
              onClick={handleUpdate}>{t("update")}</Button>
      </div>
    </form>
  </Panel>;
};

export default ConnectionDetails;
