import { connect } from 'react-redux';
import pentaActions from '../modules/pentaui/actions';
import rockhopperActions from '../modules/rockhopper/actions';
import JogMenu from '../components/JogMenu';
import { selectJogSpeedDefaults, selectJogSpeedLimits, selectUnitsCode, selectDelayedFeedratePercentage, selectDelayedClientConfig, selectDelayedJogConfig } from '../modules/rockhopper/selectors';
// import { selectJogDisplay } from '../modules/pentaui/selectors';


export default connect( 
(state, ownProps) => {
  return { 
    clientConfig: selectDelayedClientConfig(state),
    // display: selectJogDisplay(state),
    feedrate: selectDelayedFeedratePercentage(state),
    jogSpeedDefaults: selectJogSpeedDefaults(state),
    jogSpeedLimits: selectJogSpeedLimits(state),
    jogConfig: selectDelayedJogConfig(state),
    unitsCode: selectUnitsCode(state),
  }
},
(dispatch, ownProps) => ({
  onSetTestVal: (val) => {
    dispatch( rockhopperActions.delayed.update.testVal({ data: val }));
  },

  onSetDisplay: (val) => dispatch(pentaActions.jog.setDisplay(val)),

  onSetJogConfig: (client, jog) => {
    // dispatch(rockhopperActions.updateHelpers.jogConfig({data: config}));
    dispatch(rockhopperActions.delayed.update.clientConfig({data: client }));
    dispatch(rockhopperActions.delayed.put.saveClientConfig({ data: [ 'jogConfig', jog ] }));
  },
}))(JogMenu);
