import Grid from '@material-ui/core/Grid';
import IconButton from '@material-ui/core/IconButton';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import ResetIcon from '@material-ui/icons/RotateLeft';
import Select from '@material-ui/core/Select';
import Slider from '@material-ui/core/Slider';
import { makeStyles } from '@material-ui/styles';

import KeyboardJoggingSwitch from '../containers/KeyboardJoggingSwitch';
import MouseWheelJoggingSwitch from '../containers/MouseWheelJoggingSwitch';
import SubmittableTextField from './SubmittableTextField';

import useTranslation from '../hooks/useTranslation';
import useSound from '../hooks/useSound';
import useUnits, { toMachineUnits } from '../hooks/useUnits';
import { UNITS } from '../modules/rockhopper/constants';

