import React, {useState} from 'react';

import Box from '@material-ui/core/Box';
import Button from '@material-ui/core/Button';
import ButtonGroup from '@material-ui/core/ButtonGroup';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import HelpIcon from '@material-ui/icons/Help';
import Switch from '@material-ui/core/Switch';
import Tooltip from '@material-ui/core/Tooltip';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/styles';

import useTranslation from '../hooks/useTranslation';

const useStyles = makeStyles((theme) => ({
  buttonGroup: {
    display: "flex",
    marginLeft: "auto",
    float: "right"
  },
  label: {
    ...theme.typography.sectionHeader,
    fontSize: 14,
    marginLeft: theme.spacing(1),
  },
  pending: {
    fontSize: 14,
    marginLeft: theme.spacing(1),

  },
  statusItem: {
    ...theme.typography.statusItem,
    fontSize: 14,
    marginLeft: theme.spacing(1),
  },
}));

const MachineConfigExtras = (props) => {
  const {
    visible,
    value,
    index,
    boardRevision,
    toolProbeOnM6,
    onSetM6ToolProbe,
    onToggleMachineVersionV1,
  } = props;

  const { t } = useTranslation();
  const classes = useStyles();
  const [pendingChanges, setPendingChanges] = useState();
  const pendingM6 = pendingChanges && pendingChanges.toolProbeOnM6;
  const pendingBoardRevision = pendingChanges && pendingChanges.boardRevision;

  const machineVersionString = pendingChanges && pendingChanges.boardRevision ? (
    pendingChanges.boardRevision
  ) : (
    boardRevision 
  );
  const machineVersionButtonString = machineVersionString === "v1revH" ? "v2revP" : "v1revH";

  const m6checked = pendingChanges && pendingChanges.toolProbeOnM6 ? (
    pendingChanges.toolProbeOnM6 === '1' ? true : false
  ) : (
    toolProbeOnM6 === '1' ? true : false
  );

  const handleDiscardChanges = () => {
    setPendingChanges();
  };
  
  const handleCommitChanges = () => {
    if(pendingChanges.boardRevision){
      onToggleMachineVersionV1();
    }
    if(pendingChanges.toolProbeOnM6){
      onSetM6ToolProbe(pendingChanges.toolProbeOnM6);
    }
  };

  const handleToggleMachineVersionV1 = () => {
    if(pendingChanges && pendingChanges.boardRevision){
      const {boardRevision, ...otherChanges} = pendingChanges;
      if(Object.keys(otherChanges).length > 0){
        setPendingChanges(otherChanges);
      }
      else{ setPendingChanges() }
    }
    else{
      setPendingChanges({boardRevision: machineVersionButtonString, ...pendingChanges})
    }
  };

  const handleToggleM6 = (e) => {
    if(pendingChanges && pendingChanges.toolProbeOnM6){
    const {toolProbeOnM6, ...otherChanges} = pendingChanges;
      if(Object.keys(otherChanges).length > 0){
        setPendingChanges(otherChanges);
      }
      else{ setPendingChanges() }
    }
    else{
    setPendingChanges({toolProbeOnM6: toolProbeOnM6 === '1' ? '0' : '1', ...pendingChanges})
    }
  };
  
  return <div
    role="tabpanel"
    hidden={value !== index}
    id={`simple-tabpanel-${index}`}
    aria-labelledby={`simple-tab-${index}`}
    // {...other}
  >
  {visible && (
    <>
      <Box className={classes.iniRoot}>
        <List>
          {/* <ListItem><label className={classes.label}>{t("probeToolOnM6")} <ToolProbeOnM6Switch color="primary"/></label></ListItem> */}
          <ListItem>
            <label className={classes.label}>
              {t("probeToolOnM6") + ": "}
              <Switch color={pendingM6 ? "secondary" : "primary"} checked={m6checked} onChange={handleToggleM6}/>
            </label>
            {pendingChanges && pendingChanges.toolProbeOnM6 && <Typography className={classes.pending} color="secondary">{t("pending")}</Typography>}
          </ListItem>
          <ListItem>
            <Typography className={classes.label}>{t("machineVersion") + ": "}</Typography> 
            <Typography className={classes.statusItem} color={pendingBoardRevision ? "secondary" : "textPrimary"}> {machineVersionString}</Typography>
            {pendingChanges && pendingChanges.boardRevision && <Typography className={classes.pending} color="secondary">{t("pending")}</Typography>}
          </ListItem>
          { boardRevision === "v2revP" || boardRevision === "v1revH" ?
          <ListItem>
            <Button variant="outlined" size="small" color="primary" onClick={handleToggleMachineVersionV1}>{t("setMachineVersionTo")} {machineVersionButtonString}</Button>
            <Tooltip placement="right" title={t("tooltip_ToggleMachineVersionV1")}><HelpIcon/></Tooltip>
          </ListItem> : null }
          <ListItem>
            <span className={classes.buttonGroup}>
              <ButtonGroup disabled={!pendingChanges}>
                <Button onClick={handleDiscardChanges}>
                  {t("discard")}
                </Button>
                <Button onClick={handleCommitChanges}>
                  {t("applyAndRestart")}
                </Button>
              </ButtonGroup>
            </span>
          </ListItem>
        </List>
      </Box>
    </>
  )}
</div>
};
export default MachineConfigExtras;
