import { connect } from 'react-redux';
import Spindle from '../components/Spindle';
import actions from '../modules/rockhopper/actions';

import { selectMeasuredSpindleSpeed,
         selectMaximumSpindleSpeed,
         selectSpindlePower,
         selectSpindleLoad,
         selectSpindleTemperature,
         selectSpindlePressure,
         selectHasHss,
         selectHssWarmUpNeeded,
         selectReadyForMdi } from '../modules/rockhopper/selectors';

export default connect( 
(state, ownProps) => {
  return { 
    rpm: selectMeasuredSpindleSpeed(state),
    maxRPM: selectMaximumSpindleSpeed(state),
    power: selectSpindlePower(state),
    load: selectSpindleLoad(state),
    temperature: selectSpindleTemperature(state),
    pressure: selectSpindlePressure(state),
    needsWarmUp: selectHssWarmUpNeeded(state),
    showWarmUpButton: selectHasHss(state),
    disableWarmUpButton: !(selectReadyForMdi(state).isReady),
  }
}, 
(dispatch, ownProps) => ({ 
  onWarmUp: () => {
    dispatch(actions.put.mdiCmd({ data: [ "M672" ] }));
  }
})
)(Spindle);
