import React from 'react';
import { useTranslation } from 'react-i18next';
import { makeStyles } from '@material-ui/core/styles';
import CircularProgress from '@material-ui/core/CircularProgress';
import DownloadIcon from '@material-ui/icons/GetApp';
import HelpIcon from '@material-ui/icons/Help';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListSubheader from '@material-ui/core/ListSubheader';
import { Tooltip, Typography } from '@material-ui/core';


const useStyles = makeStyles((theme) => ({
  list: {
    border: '1px solid lightgrey',
    maxHeight: 300,
    overflow: "scroll",
    padding: 0,
    width: 300,
  },
  listItemSubheader: {
    backgroundColor: theme.palette.background.default
  },
  listItemIcon: {
    marginBottom: -6
  },
}))

const DownloadLogsList = (props) => {
  const {
    lsLogs,
    includeSubheader=true,
    includeZip=true,
    onDownloadLogs,
    onDownloadLogFile,
  } = props;

  const { t } = useTranslation();
  const classes = useStyles();

  const handleClickZip = () => {
    if(onDownloadLogs){
      onDownloadLogs();
    }
  }

  const handleClickFile = (filename) => {
    if(onDownloadLogFile){
      onDownloadLogFile(filename);
    }
  }


  return <List 
        className={classes.list}
        subheader={
          includeSubheader && 
          <ListSubheader component="div" id="nested-list-subheader" className={classes.listItemSubheader}>
            <span>
              <Typography variant="subtitle2">
                { t("Select files to download") }
                <DownloadIcon className={classes.listItemIcon}/>
              </Typography>
            </span>
          </ListSubheader>
        }
      >
        {
          includeZip && 
          <ListItem key='zip' onClick={() => handleClickZip()}>
            <Tooltip title="Download all logs files bundled into a ZIP archive"><span >{t("zipArchive")}<HelpIcon className={classes.listItemIcon}/></span></Tooltip>
          </ListItem>
        }
        { 
          lsLogs && lsLogs.length > 0 ? (
            lsLogs.map((filename) => {
              return (
                <ListItem key={filename} button onClick={() => handleClickFile(filename)}>
                  <span >{filename}</span>
                </ListItem>
              );
            })
          ):(
            <ListItem key={'fetching'}>
              <span >{t("fetchingLsLogs")}<CircularProgress/></span>
            </ListItem>
          )
        }
      </List>
    };
export default DownloadLogsList;
