import { MODULE_ID } from './constants';
import { createActions } from 'redux-actions';

const actions = createActions({
  RECEIVE_CALIB_MESSAGE: undefined,
  SEND_CALIB_MESSAGE: undefined,
  
  SET_CONNECTION_DETAILS: (host, port) => ({ host, port }),
  SET_CONNECTED: undefined,
  SET_CMM_CONNECTED: undefined,
  SET_SERIAL_NUM: (serialnum) => ({serialnum}),
  UPDATE_CALIB_STATUS: (calibStatus) => (calibStatus),

  START_CALIB_PROCESS: (serialnum, variant) => ({serialnum, variant}),
}, { prefix: MODULE_ID });

export default actions;
