export const getDroDisplayData = (display, positions) => {
  switch(display) {
    case "velocity": 
      return positions.map(({velocity, ...data}) => {
        return velocity
      })
    case "dtg": 
      return positions.map(({dtg, ...data}) => {
        return dtg
      })
    case "position":
      return positions.map(({position, ...data}) => {
        return position
      })
    case "actualPosition":
      return positions.map(({actualPosition, ...data}) => {
        return actualPosition
      })
    default: //shouldn't ever be called but including to silence React warning... and just in case, it provides a sensible fallback
      return positions.map(({position, ...data}) => {
        return position
      })
  }
}

export const smallValueFilter = (val, decimals=5) => {
  const valToFixed = val.toFixed(decimals);
  if(parseFloat(valToFixed) === 0){
    return Math.abs(val) 
  }
  else{
    return val;
  } 
} 