import actions from './actions';
import { LOCALSTORAGE_KEY } from './constants';
import { cancel, join, actionChannel, race, delay, fork, takeEvery, take, put, select, call } from 'redux-saga/effects';
import { eventChannel } from 'redux-saga';
import { selectWebSocket } from './selectors';

export const createWebSocket = (host, port, encrypted) => {
  try {
    return new WebSocket(`ws${ encrypted ? 's' : '' }://${host}:${port}/`);
  } catch {
    console.log('setting')
    put(actions.setCmmConnected(false));
    return null;
  }
};

export const waitForWebSocketConnection = async (socket) => {
  await new Promise((resolve) => {
    const timerID = setTimeout(() => {
      socket.removeEventListener("open", onOpen);
      socket.close();
      resolve();
    }, 5000);
    const onOpen = () => {
      clearTimeout(timerID);
      socket.removeEventListener("open", onOpen);
      resolve();
    };
    socket.addEventListener("open", onOpen);

  });
};

const sendWebSocketMessage = (socket, message) => {
  socket.send(JSON.stringify(message));
};

export const closeWebSocket = (socket) => {
  if(socket.readyState !== WebSocket.CLOSED && socket.readyState !== WebSocket.CLOSING) {
    socket.close();
  }
};

const createCalibEventChannel = (socket) => {
  return eventChannel(emit => {
    const onMessage = (e) => {
      const data = JSON.parse(e.data);

      emit(data);
    };

    const onClose = () => {
      emit(new Error("Calib websocket closed."));
    };

    socket.addEventListener("message", onMessage);
    socket.addEventListener("close", onClose);

    return () => {
      socket.removeEventListener("message", onMessage);
      socket.removeEventListener("close", onClose);
    };
  });
};

export function *sendCalibMessage(socket, action) {
  yield call(sendWebSocketMessage, socket, action.payload);
}

export function *sendCalibMessages(socket) {
  yield takeEvery([ actions.sendCalibMessage ], sendCalibMessage, socket);
}

export function *receiveCalibMessages(socket) {
  const calibChannel = yield call(createCalibEventChannel, socket);


  while(true) {
    try {
      const msg = yield take(calibChannel);

      if(msg.calibStatus) {
        yield put(actions.updateCalibStatus( msg.calibStatus ));
      }
      else{
        console.log('unknown calib-service message');
        console.log(msg);
      }
    } catch(e) {
      console.log("caught error", e);
      calibChannel.close();
    }
  }
}

export function *connectToCalib() {
  const {
    host,
    port
  } = yield select(selectWebSocket);

  const socket = yield call(createWebSocket, host, port);

  yield call(waitForWebSocketConnection, socket);

  if(socket && socket.readyState === WebSocket.OPEN) {
    yield put(actions.setConnected(true));
//    yield call(registerDebugCallbacks, socket);

    const sendTask = yield fork(sendCalibMessages, socket);
    const receiveTask = yield fork(receiveCalibMessages, socket);

    yield join(receiveTask);
    yield cancel(sendTask);
    yield put(actions.setConnected(false));

    yield call(closeWebSocket, socket);
  }
  else{
    yield put(actions.setConnected(false));
    yield put(actions.setCmmConnected(false));
  }
};

export function* alwaysReconnect() {
  const connectionDetailsChannel = yield actionChannel(actions.setConnectionDetails);
  while(1) {
    const result = yield race({ updateConnectionDetails: take(connectionDetailsChannel),
                                connection: call(connectToCalib)});
    if(result.updateConnectionDetails) {
      // connection details changed, store them  to local storage,
      // then reconnect
      yield call(storeConnectionDetailsToLocalStorageSaga);
    } else {
      // otherwise the connection failed (either errored,  closed, or never successfully connected)
      // delay a bit before trying again
      yield delay(1000);
    }
  }
}

const storeConnectionDetailsToLocalStorage = (host, port) => {
  localStorage.setItem(LOCALSTORAGE_KEY, JSON.stringify({ host, port }));
};

export function* storeConnectionDetailsToLocalStorageSaga() {
  const {
    host,
    port
  } = yield select(selectWebSocket);
  yield call(storeConnectionDetailsToLocalStorage, host, port);
}

