import { connect } from 'react-redux';
import SoundConfig from '../components/SoundConfig';
import actions from '../modules/rockhopper/actions';
import { selectDelayedClientConfig, selectDelayedSoundConfig } from '../modules/rockhopper/selectors';


export default connect( 
(state, ownProps) => {
  return {
    clientConfig: selectDelayedClientConfig(state),
    soundConfig: selectDelayedSoundConfig(state),
  }
}, 
(dispatch, ownProps) => ({
  onSetVolume: (volume) => {
    dispatch( actions.updateHelpers.sound.volume(volume) );
    dispatch( actions.put.saveClientConfig({ data: [ 'soundConfig', volume ] }) );
  },
  onSetClientConfig: (newClientConfig, newSoundConfig) => {
    dispatch(actions.delayed.update.clientConfig({data: newClientConfig }));
    dispatch(actions.delayed.put.saveClientConfig({ data: [ 'soundConfig', newSoundConfig ] }));
  },
  onSetSoundConfig: (newClientConfig, newSoundConfig) => {
    dispatch(actions.delayed.update.clientConfig({data: newClientConfig }));
    dispatch(actions.delayed.put.saveClientConfig({ data: [ 'soundConfig', newSoundConfig ] }));
  }
}))(SoundConfig);
