import React from 'react';

import Box from '@material-ui/core/Box';
import Button from '@material-ui/core/Button';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';

import { makeStyles } from '@material-ui/styles';

import SubmittableTextField from './SubmittableTextField';

const useStyles = makeStyles((theme) => ({
  button: {
    border: "none",
    height: "100%",
    float: "right",
    textAlign: "center",
    padding: 0,
    minWidth: 32,
  },
  container: {},
  dropDownIcon: {
    height: "100%",
  },
  textField: {
    display: "flex",
    height: "100%",
  },
}));

const DropDownSubmittableTextField = (props) => {
  const {
    value,
    onChange,
    onSelect,
    onSubmit,
    disabled,
    menuItems,
    // textFieldProps,
  } = props;

  const classes = useStyles(props);

  const [menuAnchor, setMenuAnchor] = React.useState(null);

  const closeMenu = () => {
    setMenuAnchor(null);
  };

  const handleItemClick = (index, val) => {
    setMenuAnchor(null);
    if(onChange){
      onSelect({index: index, value: val})
    }
  }

  const handleSubmit = (event) => {
    setMenuAnchor(null);
    if(onSubmit){
      onSubmit(event)
    }
  }

  return (
    <>
    <Menu
      {...props.menuProps}
      anchorEl={menuAnchor}
      open={Boolean(menuAnchor)}
      onClose={() => {setMenuAnchor(null)}}
    >
      { 
        menuItems.map( 
          (item, index) => {
            return(
              <MenuItem
                key={item}
                onClick={() => handleItemClick(index, item)}
                onKeyDown={(e) => { if(e.key === "Enter") { closeMenu(); e.preventDefault(); } }}
              >
                {item}
              </MenuItem>
            )
          }
        )
      }
    </Menu>

    <Box
      className={classes.container}
      {...props.containerProps}
    >
      <Button
          {...props.buttonProps}
          className={classes.button}
          disabled={disabled}
          onClick={(event) => {
            if(!disabled){
              setMenuAnchor(event.currentTarget)
            }
          }}
        >
          <ExpandMoreIcon 
            {...props.iconProps}
            className={classes.dropDownIcon}
          />
        </Button>
        <SubmittableTextField
          {...props.textFieldProps}
          className={classes.textField}
          disabled={disabled}
          value={value}
          onSubmit={(event, value) => handleSubmit(event, value)}
          onChange={(value) => onChange(value)}
        />
        
      </Box>
    </>
  )
};
export default DropDownSubmittableTextField;
