import actions from './actions';
import { handleActions } from 'redux-actions';
import produce from 'immer';
import { LOCALSTORAGE_KEY, CALIB_STATUS } from './constants';

const ipAddr = process.env.REACT_APP_IP_ADDR ? process.env.REACT_APP_IP_ADDR : "192.168.6.2";
const port = process.env.REACT_APP_CALIB_PORT ? process.env.REACT_APP_CALIB_PORT : "8081";
const localStorageData = JSON.parse(localStorage.getItem(LOCALSTORAGE_KEY) || "{}");
export const defaultState = {
  connected: false,
  webSocket: {
    host: localStorageData.host || (document.location.hostname.indexOf("localhost") === -1 ? document.location.hostname : ipAddr),
    port: localStorageData.port || port
  },
  serviceStatus: {
    selectedProcess: ""
  },
  calibStatus: { ...CALIB_STATUS }
};

const reducer = handleActions({
  [actions.setConnected]: produce((state,action) => {
    state.connected = action.payload;
  }),
  [actions.receiveCalibMessage]: (state,action) => {
    return { ...state, ...action.payload };
  },
  [actions.setConnectionDetails]: produce((state,action) => {
    const { 
      host,
      port
    } = action.payload;

    state.webSocket.host = host;
    state.webSocket.port = port;
  }),
  [actions.setSerialnum]: produce((state,action) => {
    console.log('setting serialnum')
    console.log(action.payload)
    state.serialnum = action.payload;
    state.calibStatus.serial.completed = true;
  }),
  [actions.setCalibProcess]: produce((state,action) => {

  }),
  [actions.startCalibProcess]: produce((state,action) => {
    console.log('starting')
    state.serialnum = action.payload.serialNum;
    state.variant = action.payload.variant;
    state.calibStatus.serial.completed = true;
    state.calibStatus.connect.completed = true;
    state.calibStatus.cmm_setup.completed = true;
  }),
  [actions.pauseCalibProcess]: produce((state,action) => {
  }),

  [actions.terminateCalibProcess]: produce((state,action) => {
  }),
  [actions.updateCalibStatus]: produce((state,action) => {
    state.calibStatus = action.payload;
  }),
  [actions.setCmmConnected]: produce((state, action) => {
    state.cmmConnected = action.payload;
  })

}, defaultState);

export default reducer;
