import React from 'react';
import ButtonBase from '@material-ui/core/ButtonBase';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import SvgIcon from '@material-ui/core/SvgIcon';
import { useTheme, makeStyles } from '@material-ui/core/styles';
import StatusBar from '../containers/StatusBar';
import useMeasure from 'react-use-measure';
import { useTranslation } from 'react-i18next';

const useStyles = makeStyles((theme) => ({
  button: {
  },
  bottomBar: {
    position: "fixed",
    bottom: 0,
    left: 0,
    width: "100%",
    backgroundColor: "rgba(0,0,0,0)"
  }
}));

export const barQuantities = (width, height=63, gap=8, beginCurvePct=.3, rounding=6, angle=70*Math.PI/180) => {
  const beginCurve = beginCurvePct*width;
  const curveTop = 0;
  const curveBottom = height;
  const xt = rounding*Math.sin(angle);
  const yt = rounding+rounding*Math.cos(angle);
  const xb = rounding*Math.sin(angle);
  const yb = rounding-rounding*Math.cos(angle);
  const tanAngle = Math.tan(angle);
  const midY = height-yt-yb;
  const midX = midY/tanAngle;
  const gt = yt/tanAngle;
  const gb = yb/tanAngle;
  const bottomWidth = beginCurve+xt-midX-xb;

  return {
    width,
    height,
    gap,
    beginCurve,
    beginCurvePct,
    rounding,
    angle,
    curveTop,
    curveBottom,
    xt,
    yt,
    xb,
    yb,
    tanAngle,
    midY,
    midX,
    gt,
    gb,
    bottomWidth
  };
};

export const BarContext = React.createContext({});

const BottomBar = (props) => {
  const classes = useStyles();
  const { t } = useTranslation();

  const {
    buttonLabel,
    buttonColor,
    onButtonClick
  } = props;

  const [ divRef, { width }] = useMeasure();
  const height = 63;
  const theme = useTheme();
  const isSmall = useMediaQuery(theme.breakpoints.down('sm'));

  const contextVars = barQuantities(width,height,theme.spacing(1));
  const {
    curveTop,
    beginCurve,
    rounding,
    xt,
    yt,
    midX,
    midY,
    xb,
    yb,
    gt,
    gb,
    curveBottom,
    gap
  } = contextVars;

  // background of the left side of the status bar
  const leftPath = [
    [ "M", 0, curveTop ],
    [ "L", beginCurve, curveTop ],
    [ "a", rounding, rounding, 0, 0, 1, xt, yt ],
    [ "l", -midX, midY ],
    [ "a", rounding, rounding, 0, 0, 1, -xb, yb ],
    [ "L", 0, curveBottom ],
    [ "Z" ]
  ];

  // background of the right side of the status bar
  const rightPath = [
    [ "M", width, curveTop ],
    [ "L", width-beginCurve, curveTop ],
    [ "a", rounding, rounding, 0, 0, 0, -xt, yt ],
    [ "l", midX, midY ],
    [ "a", rounding, rounding, 0, 0, 0, xb, yb ],
    [ "L", width, curveBottom ],
    [ "Z" ]
  ];

  // background of center button
  const centerPath = [
    [ "M", beginCurve+xt+gt+xb-gb+gap, curveTop ],
    [ "a", rounding, rounding, 0, 0, 0, -xb, yb ],
    [ "l", -midX, midY ],
    [ "a", rounding, rounding, 0, 0, 0, xt, yt ],
    [ "L", width-beginCurve-xt+midX+gb-xt-gt-gap, curveBottom ],
    [ "a", rounding, rounding, 0, 0, 0, xt, -yt ],
    [ "l", -midX, -midY ],
    [ "a", rounding, rounding, 0, 0, 0, -xb, -yb ],
    [ "Z" ]
  ];

  // Shapes that covers the rectangular portion of the ButtonBase that don't overlap with centerPath so that only the shape
  // of the centerPath is clickable. Also visually covers the portion of the ripple effect that extends outside
  // the centerPath shape (because the button portion of ButtonBase is rectangular and the centerPath is not).
  const leftCover = [
    centerPath[0],
    centerPath[1],
    centerPath[2],
    centerPath[3],
    [ "L", 0, curveBottom ],
    [ "L", 0, curveTop ],
    [ "Z" ]
  ];
  const rightCover = [
    [ "M", width, curveBottom ],
    centerPath[4],
    centerPath[5],
    centerPath[6],
    centerPath[7],
    [ "L", width, curveTop ],
    [ "Z" ]
  ];

  const buttonLeft = (centerPath[0][1]+centerPath[1][6]+centerPath[2][1]+centerPath[3][6]-rounding)
  const buttonWidth = (centerPath[4][1]+rounding)-buttonLeft;

  return <BarContext.Provider value={contextVars}>
  <div ref={divRef} className={classes.bottomBar}>
    <ButtonBase focusRipple className={classes.button} style={ { left: buttonLeft, height: height-1 } } onClick={onButtonClick}>
      <SvgIcon width={width} height={height} viewBox={buttonLeft + " 0 " + buttonWidth + " " + height} style={{ width: buttonWidth, height, left: buttonLeft }}>
        <path d={centerPath.map((cmd) => cmd.join(" ")).join(" ")} 
              fill={theme.palette[buttonColor].main}/>
        <text x={width*.5} y={height*.5} style={ (isSmall ? theme.typography.bottomButtonSm : theme.typography.bottomButton) } textAnchor="middle" alignmentBaseline="middle">{ t(buttonLabel) }</text>
      </SvgIcon>
    </ButtonBase>
    <svg width={width} height={height} style={ { display: "block", height, position: "fixed", bottom: 0, zIndex: 100 }} pointerEvents="none">
      <path d={leftCover.map((cmd) => cmd.join(" ")).join(" ")} 
            fill={theme.palette.background.paper} pointerEvents="visible"/>
      <path d={rightCover.map((cmd) => cmd.join(" ")).join(" ")} 
            fill={theme.palette.background.paper} pointerEvents="visible"/>
      <path d={leftPath.map((cmd) => cmd.join(" ")).join(" ")} 
            fill={theme.palette.text.primary}/>
      <path d={rightPath.map((cmd) => cmd.join(" ")).join(" ")} 
            fill={theme.palette.text.primary}/>
    </svg>

    <StatusBar/>
  </div>
  </BarContext.Provider>
};

export default BottomBar;
