import { useContext, useEffect, useMemo } from 'react';
import { SoundContext } from '../components/SoundProvider';
import { Howl, Howler } from 'howler';


const useSound = () => {
  const soundConfig = useContext(SoundContext);
  
  Howler.autoSuspend = false;
  const tick = useMemo(() => new Howl({ src: process.env.PUBLIC_URL + "/sounds/tick.wav" }), []); 

  useEffect(() => {
    tick.volume(soundConfig.volume);
    tick.mute( !soundConfig.enabled );
  }, [tick, soundConfig])
  
  const playTick = () => { tick.play() }
  const setVolume = (val) => { tick.volume(val) }
  const setEnabled = (val) => { tick.mute(!val) }

  return { playTick, setVolume, setEnabled }
}
export default useSound;