import React from 'react';
import useSound from '../hooks/useSound';
import useUnits from '../hooks/useUnits';
import { getDroDisplayData, smallValueFilter } from '../util/functions';
import IncrementalFloatingPointSpinner from './IncrementalFloatingPointSpinner';
import useTranslation from '../hooks/useTranslation';

const JogSpinners = (props) => {
  const {
    homed,
    jointLimits,
    positions,
    openDigitIndex,
    openAxis,
    display,
    jogMode,
    modifierKeys,
    mouseWheelJogs,
    onSetOpenAxis,
    onSetOpenDigitIndex,
    onJogIncrement,
    onHome
  } = props;

  const { playTick } = useSound();
  const { toDisplayUnits } = useUnits();
  const { t } = useTranslation();

  const displayData = getDroDisplayData(display, positions);
  const spinners = positions.map(({ axis, position, actualPosition, jointActualPosition, jointIndex, index }, ) => {
    // const speedMagnitude = jointIndex > 2 ? 10 : 1;
    const isJointHomed = homed[jointIndex];
    const handleChange = (e,v) => {
      const modifiers = { 'Ctrl' : e.ctrlKey, 'Shift' : e.shiftKey, 'Alt': e.altKey, 'Meta': e.metaKey};
      const slow = modifiers[modifierKeys['slow']];
      // const baseSpeed = jointIndex > 2 ? jogConfig.angularSpeed : jogConfig.linearSpeed;
      // const speed = slow ? baseSpeed * jogConfig.slowJogFraction : baseSpeed;
      // const speed = speedMagnitude * Math.sign(v);
      
      const inc = {value: Math.abs(v), unit: toDisplayUnits(position).unit};
      onJogIncrement({ axis: index, joint: jointIndex, direction: Math.sign(v), increment: inc , playTick: playTick, slow});
    };
    const handleHome = (axis) => () => {
      onHome(axis);
    };
    const fractionalDigits = jointIndex > 2 ? 3 : ( toDisplayUnits(position).unit === 'mm' ? 3 : 4 );
    const wholeDigits = jointIndex > 2 ? 5 : ( toDisplayUnits(position).unit === 'mm' ? 5 : 4 );
    const maxOpenIndex = jointIndex > 2 ? 5 : ( toDisplayUnits(position).unit === 'mm' ? 4 : 4 );
    const currentOpenIndex = openAxis === axis ? openDigitIndex : -1;
    const handleSetOpenIndex = (index) => {
      onSetOpenAxis(axis)
      onSetOpenDigitIndex(index)
    };
    const handleSetOpenAxis = () => {
      onSetOpenAxis(axis)
    };
    const filteredVal = smallValueFilter(displayData[jointIndex].value);
    const spinnerValue = toDisplayUnits({value: filteredVal, unit: displayData[jointIndex].unit}).value;
    const areLimitsViolated = jogMode === "AXIS" && jointLimits && jointLimits.length > 0 && (jointActualPosition.value > jointLimits[jointIndex].max || jointActualPosition.value < jointLimits[jointIndex].min);
    return <div key={axis} onPointerDown={() => handleSetOpenAxis(axis)} >
      <IncrementalFloatingPointSpinner 
                                            label={axis} 
                                            value={spinnerValue}
                                            unit={toDisplayUnits(position).unit}
                                            onChange={handleChange} 
                                            fractionalDigits={fractionalDigits} 
                                            wholeDigits={wholeDigits} 
                                            maxOpenIndex={maxOpenIndex}
                                            highlighted={axis === openAxis}
                                            openIndex={currentOpenIndex}
                                            captureWheelOnDocument={mouseWheelJogs}
                                            onSetOpenIndex={handleSetOpenIndex}
                                            homed={isJointHomed}
                                            onHome={handleHome(axis)}
                                            showWarning={areLimitsViolated}
                                            warningText={t("tooltip_SoftLimitWarning")}
                                            />
      </div>
  });

  return <div>
  {spinners}
  </div>;
};
export default JogSpinners;
