import { connect } from 'react-redux';
import actions from '../modules/rockhopper/actions';
import { selectSystemStatus, selectIsLoggedIn } from '../modules/rockhopper/selectors';
import SystemStatus from '../components/SystemStatus';

export default connect( 
(state, ownProps) => {
  return { 
    systemStatus: selectSystemStatus(state),
    loggedIn: selectIsLoggedIn(state)
  }
}, 
(dispatch, ownProps) => ({ 
  onStatus: () => dispatch(actions.get.systemStatus()),
  onClearLogs: () => {
    dispatch(actions.put.clearLogs());
    dispatch(actions.get.systemStatus());
  },
}))(SystemStatus);
