import React from 'react';
import Button from '@material-ui/core/Button';
import Tooltip from '@material-ui/core/Tooltip';
import CircularProgress from '@material-ui/core/CircularProgress';
import useTranslation from '../hooks/useTranslation';

function DownloadLogsButton(props) {
  const {
    isLogsBundleInProgress,
    onDownloadLogs
  } = props;
  
  const { t } = useTranslation();

  const handleDownloadClick = () => {
    if(onDownloadLogs){
      onDownloadLogs();
    }
  }

  return <Tooltip title={t("downloadLogsTooltip")} enterDelay={500}>
    <Button onClick={handleDownloadClick} variant="outlined">{t("downloadLogArchive")}{isLogsBundleInProgress && <CircularProgress size={20}/>}</Button>
  </Tooltip>
}

export default DownloadLogsButton;
