import { useCallback } from 'react';
import Panel from './Panel';
import CircularProgress from '@material-ui/core/CircularProgress';
import useTranslation from '../hooks/useTranslation';
import Button from '@material-ui/core/Button';
import { makeStyles } from '@material-ui/styles';
import DisplayIf from './DisplayIf';
import { ACTIVE } from '../modules/kinetic/constants';

const useStyles = makeStyles((theme) => ({
  active: {
    fontWeight: "bold",
    fontSize: 18,
    marginBottom: theme.spacing(1)
  },
  latest: {
    marginBottom: theme.spacing(1)
  },
  checkForLatest: {
    marginTop: theme.spacing(1)
  },
  upToDate: {
    marginTop: theme.spacing(1)
  },
  machineActions: {
    marginTop: theme.spacing(1)
  },
  shutdownMachine: {
    marginLeft: theme.spacing(1)
  },
  shutdownServices: {
    marginTop: theme.spacing(1)
  },
  latestError: {
    marginLeft: theme.spacing(1)
  },
  deployCommitError: {
    marginLeft: theme.spacing(1)
  },
  upgradeToCommitError: {
    marginLeft: theme.spacing(1)
  }
}));

const Version = (props) => {
  const {
    connected,
    onDeploy,
    onUpgrade,
    onCheckLatest,
    onShutdownServices,
    onRebootMachine,
    onShutdownMachine,
    getServiceStatus = {},
    getDeployments = {},
    getLatest = {},
    deployCommit = {},
    upgradeToCommit = {},
    shutdownMachine = {},
    rebootMachine = {},
    shutdownServices = {}
  } = props;

  const {
    pocketnc,
    rockhopper
  } = getServiceStatus;

  const {
    deployments = [],
    error: deploymentsError
  } = getDeployments;

  const {
    latest = {},
    loading: latestLoading,
    error: latestError
  } = getLatest;

  const {
    loading: deployCommitLoading,
    error: deployCommitError
  } = deployCommit;

  const {
    loading: upgradeToCommitLoading,
    error: upgradeToCommitError
  } = upgradeToCommit;

  const {
    loading: shutdownServicesLoading
  } = shutdownServices;

  const {
    loading: shutdownMachineLoading
  } = shutdownMachine;

  const {
    loading: rebootMachineLoading
  } = rebootMachine;

  const handleRollback = useCallback(() => {
    if(onDeploy && deployments.length > 1 && deployments[1].commit) {
      onDeploy(deployments[1].commit);
    }
  }, [ onDeploy, deployments ]);

  const handleUpgrade = useCallback(() => {
    if(onUpgrade && latest.commit) {
      onUpgrade(latest.commit);
    }
  }, [ onUpgrade, latest ]);

  const { t } = useTranslation();
  const classes = useStyles();

  const upToDate = deployments.find((deployment) => deployment.commit === latest.commit);
  const hasPending = deployments.find((deployment) => deployment.pending);

  return (<Panel label={t("software")}>
  <DisplayIf condition={!connected}>{ t("notConnectedToUpdateServer") }</DisplayIf>
  <DisplayIf condition={connected}>
    <DisplayIf condition={deploymentsError}><div>{ deploymentsError }</div></DisplayIf>
    <DisplayIf condition={hasPending}>
      <div className={classes.hasPending}>
      { t("rebootToTakeEffect") }
      </div>
    </DisplayIf>
    <DisplayIf condition={pocketnc === ACTIVE || rockhopper === ACTIVE}>
      <DisplayIf condition={!deploymentsError && deployments.length > 0}>
          <div className={classes.active}>{ (deployments[0] && !deployments[0].pending && !deployments[0].active) ? t("currentVersion") : null }
                                          { (deployments[0] && deployments[0].pending) ? t("pendingVersion") : null }
                                          { (deployments[0] && deployments[0].active) ? t("activeVersion") : null }: { deployments[0] && deployments[0].version }</div>
      </DisplayIf>
      <DisplayIf condition={!upToDate && latest.commit}>
          <div className={classes.latest}>{t("latestVersion")}: { latest.version }</div>
        { t("shutdownServicesToUpdate") }
      </DisplayIf>
      <DisplayIf condition={upToDate}>
        <div className={classes.upToDate}>
        { t("upToDate") }
        </div>
      </DisplayIf>
      <div className={classes.shutdownServices}>
      <Button variant="contained" color="primary" onClick={onShutdownServices}>{ shutdownServicesLoading ? <CircularProgress size={20}/> : null } { t("shutdownServices") }</Button>
      </div>
    </DisplayIf>
    <DisplayIf condition={!hasPending && pocketnc !== ACTIVE && rockhopper !== ACTIVE}>
      <DisplayIf condition={!deploymentsError && deployments.length > 0}>
          <div className={classes.active}>{ (deployments[0] && !deployments[0].pending && !deployments[0].active) ? t("currentVersion") : null }
                                          { (deployments[0] && deployments[0].pending) ? t("pendingVersion") : null }
                                          { (deployments[0] && deployments[0].active) ? t("activeVersion") : null }: { deployments[0] && deployments[0].version }</div>
      </DisplayIf>
      { /* if we have a latest object and it's not already in our list of deployments, show a button to upgrade */}
      <DisplayIf condition={!latestError && 
                             latest.commit && !upToDate
                             }>
        <div className={classes.latest}>
          <Button variant="contained" color="primary" onClick={handleUpgrade}>{ upgradeToCommitLoading ? <CircularProgress size={20}/> : null } { t("upgradeTo") } { latest.version }</Button>
          <span className={classes.upgradeToCommitError}>{upgradeToCommitError}</span>
        </div>
      </DisplayIf>

      <DisplayIf condition={!deploymentsError && deployments.length > 1}>
        <Button variant="contained" color="secondary" onClick={handleRollback}>{ deployCommitLoading ? <CircularProgress size={20}/> : null } { t("deploy") } { deployments[1] && deployments[1].version }</Button>
        <span className={classes.deployCommitError}>{deployCommitError}</span>
      </DisplayIf>

      <DisplayIf condition={latestError || latestLoading}>
        <div className={classes.checkForLatest}>
          <Button variant="contained" color="secondary" onClick={onCheckLatest}>{ latestLoading ? <CircularProgress size={20}/> : null } {t("checkForUpdates")} </Button>
          <span className={classes.latestError}>{latestError}</span>
        </div>
      </DisplayIf>

      <DisplayIf condition={upToDate}>
        <div className={classes.upToDate}>
        { t("upToDate") }
        </div>
      </DisplayIf>
    </DisplayIf>


    <div className={classes.machineActions}>
    <Button variant="contained" color={ hasPending ? "primary" : "secondary"} onClick={onRebootMachine}>{ rebootMachineLoading ? <CircularProgress size={20}/> : null } {t("rebootMachine")}</Button>
    <Button classes={ { root: classes.shutdownMachine } } variant="contained" color={"secondary"} onClick={onShutdownMachine}>{ shutdownMachineLoading ? <CircularProgress size={20}/> : null } {t("shutdownMachine")}</Button>
    </div>
  </DisplayIf>

  </Panel>
  );
}

export default Version;
