import { connect } from 'react-redux';
import Calib from '../components/CalibSimple';
import { selectIsLoggedIn } from '../modules/rockhopper/selectors';
import rockhopperActions from '../modules/rockhopper/actions';

import { selectCalibStatus, selectCalibPercentage, selectConnected, selectCmmConnected, selectServiceStatus, selectWebSocket } from '../modules/calib/selectors';
import actions from '../modules/calib/actions';

export default connect( 
(state, ownProps) => {
  return {
    ws: selectWebSocket(state),
    serviceConnected: selectConnected(state),
    cmmConnected: selectCmmConnected(state),
    rockhopperConnected: selectIsLoggedIn(state),
    serviceStatus: selectServiceStatus(state),
    calibStatus: selectCalibStatus(state),
    calibPercentage: selectCalibPercentage(state)
  }
}, 
(dispatch, ownProps) => ({
  onUploadFiles: (serialNum, variant) => {
    dispatch( rockhopperActions.put.startCalibUploadFiles({data: [serialNum, variant]}));
  },
  onStartCalibration: (serialNum, variant) => {
    dispatch( rockhopperActions.put.startCalibProcess({data: [serialNum, variant]}));
  },
  onStopCalibration: (serialNum) => {
    dispatch( actions.sendCalibMessage(['cmdStop']));
  },
  onPauseCalibration: (serialNum) => {
    dispatch( actions.sendCalibMessage(['cmdPause']));
  },
  onResumeCalibration: (serialNum) => {
    dispatch( actions.sendCalibMessage(['cmdResume']));
  },
  onStepCalibration: () => {
    dispatch( actions.sendCalibMessage([ "cmdStep" ]));
  },
}))(Calib);
