import { useEffect } from 'react';
import Panel from './Panel';
import useTranslation from '../hooks/useTranslation';
import Button from '@material-ui/core/Button';
import { makeStyles } from '@material-ui/styles';
import Tooltip from '@material-ui/core/Tooltip';

const useStyles = makeStyles((theme) => ({
  capitalize: {
    textTransform: "capitalize"
  },
  leftSpace: {
    marginLeft: theme.spacing(1)
  },
  label: {
    ...theme.typography.statusItem,
    fontSize: 14,
    marginTop: theme.spacing(1),
    marginBottom: theme.spacing(1)
  },
  container: {
    position: "relative",
    width: "100%",
    height: "30px",
    borderRadius: theme.spacing(1),
    border: "1px solid " + theme.palette.common.black,
    textAlign: "center",
    verticalAlign: "middle",
    textTransform: "capitalize",
    whiteSpace: "nowrap",
    overflow: "hidden"
  },
  system: {
    position: "absolute",
    overflow: "hidden",
    paddingTop: "3px",
    height: "30px",
    backgroundColor: theme.palette.grey[200],
    borderTopLeftRadius: theme.spacing(1),
    borderBottomLeftRadius: theme.spacing(1)
  },
  ncfiles: {
    position: "absolute",
    overflow: "hidden",
    height: "30px",
    paddingTop: "3px",
    backgroundColor: theme.palette.primary.main
  },
  logs: {
    position: "absolute",
    overflow: "hidden",
    height: "30px",
    paddingTop: "3px",
    backgroundColor: theme.palette.secondary.main
  },
  available: {
    position: "absolute",
    overflow: "hidden",
    height: "30px",
    paddingTop: "3px",
    backgroundColor: theme.palette.common.white,
    borderTopRightRadius: theme.spacing(1),
    borderBottomRightRadius: theme.spacing(1)
  }
}));

const SystemStatus = (props) => {
  const { 
    systemStatus={},
    onStatus,
    onClearLogs,
    loggedIn
  } = props;

  const { 
    disk = {},
    addresses = [],
    date
  } = systemStatus;

  const {
    total=1,
    other=0,
    available=0,
    logs=0,
    ncfiles=0
  } = disk;

  const classes = useStyles();
  const { t } = useTranslation();

  useEffect(() => {
    if(onStatus) {
      onStatus();
    }
  }, [ onStatus, loggedIn ]);

  const otherPct = other/total*100 + "%";
  const logsPct = logs/total*100 + "%";
  const ncfilesPct = ncfiles/total*100 + "%";
  const availablePct = available/total*100+"%";

  const logsLeft = otherPct;
  const ncfilesLeft = (other+logs)/total*100 + "%";
  const availableLeft = (other+logs+ncfiles)/total*100 + "%";

  const otherGB = (other/1000/1000).toFixed(2) + "GB";
  const logsGB = (logs/1000/1000).toFixed(2) + "GB";
  const ncfilesGB = (ncfiles/1000/1000).toFixed(2) + "GB";
  const availableGB = (available/1000/1000).toFixed(2) + "GB";

  const title = <div className={classes.capitalize}>
    <div>{ t("system") + " - " + otherGB }</div>
    <div>{ t("logs") + " - " + logsGB }</div>
    <div>{ t("programs") + " - " + ncfilesGB }</div>
    <div>{ t("available") + " - " + availableGB }</div>
  </div>;

  const dateObj = date ? new Date(date) : new Date();

  return (<Panel label={t("status")}>
  <div className={classes.label}>{ t("dateAndTime") }</div>
  <div>
  { dateObj.toLocaleDateString() + " " +  dateObj.toLocaleTimeString() }
  </div>
  <div className={classes.label}>{ t("storage") }</div>
  <Tooltip title={title}>
  <div className={classes.container}>
    <div className={classes.system}    style={ { left: "0%", width: otherPct }}>{ t("system") } - { otherGB }</div>
    <div className={classes.logs}      style={ { left: logsLeft, width: logsPct }}>{ t("logs") } - { logsGB }</div>
    <div className={classes.ncfiles}   style={ { left: ncfilesLeft, width: ncfilesPct }}>{ t("programs") } - { ncfilesGB }</div>
    <div className={classes.available} style={ { left: availableLeft, width: availablePct }}>{ t("available") } - { availableGB }</div>
  </div>
  </Tooltip>
  <div className={classes.label}>{ t("network") }</div>
  <div>
   { addresses.map((iface) => <div key={iface.iface}><b>{iface.iface}</b> {iface.ip}, {iface.mac}</div>)}
  </div>
  <br/>
  <Button onClick={onStatus} variant="outlined">{t("refresh")}</Button>
  <Button onClick={onClearLogs} variant="outlined" classes={ { root: classes.leftSpace }}>{t("clearLogs")}</Button>
  </Panel>
  );
}

export default SystemStatus;
