import React, {useState} from 'react';
import { useTranslation } from 'react-i18next';
import { makeStyles } from '@material-ui/core/styles';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import ExpandLessIcon from '@material-ui/icons/ExpandLess';
import Collapse from '@material-ui/core/Collapse';

import Panel from './Panel';
import DownloadLogsButton from '../containers/DownloadLogsButton';
import DownloadLogsList from '../containers/DownloadLogsList';
import { Typography } from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
  buttonDiv: {
    marginBottom: theme.spacing(1)
  },
  label: {
    ...theme.typography.statusItem,
    fontSize: 14,
    marginTop: theme.spacing(1),
    marginBottom: theme.spacing(1)
  },
  listButton: {
    cursor: 'pointer',
    display: 'flex',
    alignItems: 'center',
    flexWrap: 'wrap',
    userSelect: 'none',
  },
  listDiv: {
  },
  logsDiv: {
    marginTop: theme.spacing(1),
    height: 440
  }
}))

const MachineHistory = (props) => {
  const {
    onLsLogs,
    spindleSeconds,
  } = props;

  const [displayList, setDisplayList] = useState(false);
  const handleToggleFilesList = () => {
    if(onLsLogs && !displayList){
      onLsLogs()
    }
    setDisplayList(!displayList);
  }

  const { t } = useTranslation();
  const classes = useStyles();
  const spindleHours = spindleSeconds / 3600;

  return <Panel label={t("machineHistory")} >
    <div>
      <span className={classes.label}>{ t("spindleRuntime") }</span>
      <div>
        <span>
          {spindleHours.toFixed(2)}
        </span>
        <span>
          {" " + t("hours")}
        </span>
      </div>
    </div>

    <div className={classes.logsDiv}>
      <span className={classes.label}>{ t("systemLogs") }</span>
      <div className={classes.buttonDiv}>
        <DownloadLogsButton/>
      </div>
      <div className={classes.listDiv}>
        <div
          onClick={handleToggleFilesList}
          className={classes.listButton}
        >
            {displayList ?  <ExpandLessIcon/> : <ExpandMoreIcon />}
            <Typography variant="subtitle2" component="span" >
            {t("Download Individual Log Files")}
          </Typography>
        </div>

        <Collapse in={displayList}>
          <DownloadLogsList includeZip={false} includeSubheader={false}/>
        </Collapse>
      </div>
    </div>

  </Panel>;
};
export default MachineHistory;
