import React from 'react';
import produce from 'immer';

import Grid from '@material-ui/core/Grid';
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Slider from '@material-ui/core/Slider';
import Switch from '@material-ui/core/Switch';
import Typography from '@material-ui/core/Typography';
import VolumeDown from '@material-ui/icons/VolumeDown';
import VolumeUp from '@material-ui/icons/VolumeUp';
// import { makeStyles } from '@material-ui/styles';

import Panel from './Panel';
import useSound from '../hooks/useSound';
import useTranslation from '../hooks/useTranslation';


// const useStyles = makeStyles((theme) => ({
// }));

const SoundConfig = (props) => {
  const {
    clientConfig,
    soundConfig,
    onSetSoundConfig,
  } = props;

  // const classes = useStyles();
  const { playTick, setVolume, setEnabled } = useSound();
  const { t } = useTranslation();

  const handleSetEnabled = (e, val) => {
    setEnabled(val);
    playTick();
    const newSoundConfig = produce(soundConfig, (draft) => { draft.enabled = val })
    const newClientConfig = produce(clientConfig, (draft) => { draft.soundConfig = newSoundConfig })
    setSoundConfig(newClientConfig, newSoundConfig);
  }

  const handleSetVolume = (e, volumePercent) => {
    const volumeFraction = volumePercent / 100;
    setVolume(volumeFraction);
    playTick();
    const newSoundConfig = produce(soundConfig, (draft) => { draft.volume = volumeFraction })
    const newClientConfig = produce(clientConfig, (draft) => { draft.soundConfig = newSoundConfig })
    setSoundConfig(newClientConfig, newSoundConfig);
  }

  const setSoundConfig = (clientConfig, soundConfig) => {
    onSetSoundConfig(clientConfig, soundConfig);
  }

  return <Panel label={t("Audio")}>
    <Typography id="discrete-slider" gutterBottom>
      {t("volume")}
    </Typography>
      <Grid container spacing={2}>
        <Grid item>
          <VolumeDown />
        </Grid>
        <Grid item xs>
          <Slider min={0} max={100} value={soundConfig.volume * 100} onChange={handleSetVolume} aria-labelledby="continuous-slider" />
        </Grid>
        <Grid item>
          <VolumeUp />
        </Grid>
      </Grid>
      <FormGroup row>
        <FormControlLabel
          control={<Switch color="primary" checked={soundConfig.enabled} onChange={handleSetEnabled}/>}
          label={t("audioEnabled")}
          // labelPlacement="start"
        />
      </FormGroup>
  </Panel>
}
export default SoundConfig;
