import { connect } from 'react-redux';
import actions from '../modules/rockhopper/actions';
import pentaActions from '../modules/pentaui/actions';
import { selectBoardRevision, selectAxisToJointMap, selectPositions, selectHomedJoints, selectUnitsCode, selectJogMode, selectJointLimits, selectJogConfig, selectKeyboardShortcutsJogModifiers } from '../modules/rockhopper/selectors';
import { selectAxisSelectJogDigitIndex, selectAxisSelectJogAxis, selectEnableMouseWheelJogging, selectJogDisplay } from '../modules/pentaui/selectors';
import JogSpinners from '../components/JogSpinners';
import { SOLO, JOG_INCREMENT_LIMITS } from '../modules/rockhopper/constants';

import { toMachineUnits }  from '../hooks/useUnits';


export default connect( 
(state, ownProps) => {
  const positions = selectPositions(state);
  const homed = selectHomedJoints(state);
  const jogConfig = selectJogConfig(state);
  const jogMode = selectJogMode(state);
  const unitsCode = selectUnitsCode(state);
  const axisToJointMap = selectAxisToJointMap(state);
  const openDigitIndex = selectAxisSelectJogDigitIndex(state);
  const openAxis = selectAxisSelectJogAxis(state);
  const display = selectJogDisplay(state);
  const mouseWheelJogs = selectEnableMouseWheelJogging(state);
  const jointLimits = selectJointLimits(state);
  const boardRevision = selectBoardRevision(state);
  const modifierKeys = selectKeyboardShortcutsJogModifiers(state);

  return { 
    display,
    homed,
    jogConfig,
    jogMode,
    positions,
    unitsCode,
    openDigitIndex,
    openAxis,
    axisToJointMap,
    mouseWheelJogs,
    jointLimits,
    boardRevision,
    modifierKeys,
  };
},
(dispatch, ownProps) => ({
  onDispatchPutHelper: (putHelper, data) => {
    dispatch( actions.putHelpers[putHelper](data) )
  },
  onHome: (axis, joint, boardRevision) => {
    if(boardRevision !== SOLO) {
      if(typeof(axis) !== "undefined" && typeof(joint) !== "undefined") {
        dispatch(actions.put.homeCmd({ data: [ [ joint ] ] }));
      }
    } else {
      if(typeof(axis) !== "undefined" && typeof(joint) !== "undefined") {
        const feedRate = ` ABS[#<_ini[JOINT_${joint}]HOME_SEARCH_VEL>] * 60 `;
        const axisPosition = ` #<_ini[JOINT_${joint}]HOME> `
        const axisNumber = "XYZABC".indexOf(axis);
        dispatch(actions.put.mdiCmd({ data: [ `o<machine-cmd> call [ ${axisNumber} ] [ ${feedRate} ] [ ${axisPosition} ]` ] }));
      }
    }
  },
  onSetOpenAxis: (axis) => {
    dispatch(pentaActions.jog.setIncrement(null));
    dispatch(pentaActions.axisSelectJog.setAxis(axis));
  },
  onSetOpenDigitIndex: (axis) => {
    dispatch(pentaActions.jog.setIncrement(null));
    dispatch(pentaActions.axisSelectJog.setDigitIndex(axis));
  },
  onDispatchJogIncrementWarning: (increment) => {
    dispatch(actions.messages.warning(`${increment} machine-units is outside Jog Increment limits. You may have encountered a bug if you are seeing this message`))
  }
}),
(stateProps, dispatchProps, ownProps) => {
  const {
    jogConfig,
    jogMode,
    unitsCode,
    axisToJointMap,
    boardRevision
  } = stateProps;
  const {
    onDispatchPutHelper,
    onHome,
    onDispatchJogIncrementWarning
  } = dispatchProps;

  const onJogIncrement = ({ axis, joint, direction, increment, playTick, slow=false }) => {
    const baseSpeed = axis > 2 ? jogConfig.angularSpeed : jogConfig.linearSpeed;
    const speed = slow ? baseSpeed * jogConfig.slowJogFraction : baseSpeed;
    const incrementMachineUnits = toMachineUnits(increment,unitsCode);
    const limits = axis > 2 ? JOG_INCREMENT_LIMITS.ANGULAR : JOG_INCREMENT_LIMITS.LINEAR;
    if(Math.abs(increment) < limits.MIN || Math.abs(increment) > limits.MAX){
      onDispatchJogIncrementWarning(increment);
      return;
    }
    onDispatchPutHelper('jogIncrement', { axis, joint, speed: (direction * speed), increment: incrementMachineUnits.value, jogMode });
    playTick();
  }

  return {
    ...stateProps,
    ...dispatchProps,
    ...ownProps,
    onJogIncrement,
    onHome: (axis) => {
      const joint = axisToJointMap[axis];
      onHome(axis, joint, boardRevision);
    },
  }
})(JogSpinners);
