import { connect } from 'react-redux';
import KineticConnectionDetails from '../components/KineticConnectionDetails';
import actions from '../modules/kinetic/actions';
import { selectWebSocket } from '../modules/kinetic/selectors';

export default connect( 
(state, ownProps) => {
  const {
    host,
    port
  } = selectWebSocket(state);
  return { host, port }
}, 
(dispatch, ownProps) => ({ 
  onUpdate: (host, port) => {
    dispatch(actions.setConnectionDetails(host, port));
  }
}))(KineticConnectionDetails);
