import { connect } from 'react-redux';
import Version from '../components/Version';
import actions from '../modules/kinetic/actions';
import { selectConnected,
         selectDeployments, 
         selectLatest,
         selectDeployCommit,
         selectUpgradeToCommit,
         selectServiceStatus,
         selectShutdownServices,
         selectRebootMachine,
         selectShutdownMachine } from '../modules/kinetic/selectors';

export default connect( 
(state, ownProps) => {
  return { 
    connected: selectConnected(state),
    getServiceStatus: selectServiceStatus(state),
    getDeployments: selectDeployments(state),
    getLatest: selectLatest(state),
    deployCommit: selectDeployCommit(state),
    upgradeToCommit: selectUpgradeToCommit(state),
    shutdownMachine: selectShutdownMachine(state),
    rebootMachine: selectRebootMachine(state),
    shutdownServices: selectShutdownServices(state)
  }
}, 
(dispatch, ownProps) => ({ 
  onDeploy: (commit) => dispatch(actions.sendKineticMessage([ "deployCommit", commit ])),
  onUpgrade: (commit) => dispatch(actions.sendKineticMessage([ "upgradeToCommit", commit ])),
  onCheckLatest: () => dispatch(actions.sendKineticMessage([ "getLatest" ])),
  onShutdownServices: () => dispatch(actions.sendKineticMessage([ "shutdownServices" ])),
  onRebootMachine: () => dispatch(actions.sendKineticMessage([ "rebootMachine" ])),
  onShutdownMachine: () => dispatch(actions.sendKineticMessage([ "shutdownMachine" ]))
}))(Version);
