import actions from './actions';
import { handleActions } from 'redux-actions';
import produce from 'immer';
import { LOCALSTORAGE_KEY } from './constants';

const ipAddr = process.env.REACT_APP_IP_ADDR ? process.env.REACT_APP_IP_ADDR : "192.168.6.2";
const port = process.env.REACT_APP_KINETIC_PORT ? process.env.REACT_APP_KINETIC_PORT : "8080";
const localStorageData = JSON.parse(localStorage.getItem(LOCALSTORAGE_KEY) || "{}");
export const defaultState = {
  connected: false,
  webSocket: {
    host: localStorageData.host || (document.location.hostname.indexOf("localhost") === -1 ? document.location.hostname : ipAddr),
    port: localStorageData.port || port
  }
};

const reducer = handleActions({
  [actions.setConnected]: produce((state,action) => {
    state.connected = action.payload;
  }),
  [actions.receiveKineticMessage]: (state,action) => {
    return { ...state, ...action.payload };
  },
  [actions.sendKineticMessage]: produce((state,action) => {
    return { ...state, [action.payload[0]]: { "loading": true } };
  }),
  [actions.setConnectionDetails]: produce((state,action) => {
    const { 
      host,
      port
    } = action.payload;

    state.webSocket.host = host;
    state.webSocket.port = port;
  }),
}, defaultState);

export default reducer;
