import React, { useContext } from 'react';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import StatusItem from './StatusItem';
import Tooltip from '@material-ui/core/Tooltip';
import { BarContext } from './BottomBar';

import { useTheme, makeStyles } from '@material-ui/styles';

const useStyles = makeStyles((theme) => ({
  codes: {
    display: "box",
    overflow: "hidden",
    textOverflow: "ellipsis",
    boxOrient: "vertical",
    lineClamp: 2,
    maxHeight: 40
  }
}));

const ActiveCodesStatusItem = (props) => {
  const {
    activeCodes = ["G1", "G17", "G20" ]
  } = props;

  const theme = useTheme();
  const isSmall = useMediaQuery(theme.breakpoints.down('sm'));

  const { bottomWidth } = useContext(BarContext);

  const classes = useStyles();

  const allCodes = activeCodes.join(" ");
  const connectedWidth = isSmall ? 40 : 116;

  return <StatusItem>
    <Tooltip title={allCodes} arrow enterTouchDelay={0}>
    <div style={ { width: bottomWidth-connectedWidth } } className={classes.codes}>{ allCodes }</div>
    </Tooltip>
    {/*
    <Hidden mdDown>
     <Tooltip title={activeCodes.join(" ")} arrow enterTouchDelay={0}>
       <span>{ onlyImportantActiveCodes.join(" ") }</span>
     </Tooltip>
   </Hidden>
   <Hidden lgUp>
     <Tooltip title={activeCodes.join(" ")} arrow enterTouchDelay={0}>
       <span>{t("activeCodes")}</span>
     </Tooltip>
   </Hidden>*/}
  </StatusItem>
};

export default ActiveCodesStatusItem;
