import { connect } from 'react-redux';
import GCodeEditor from '../components/GCodeEditor';
import { selectCurrentLine, selectCurrentFileProgress, selectProgramKey, selectCurrentFileChecksum } from '../modules/rockhopper/selectors';
import downloadQueue from '../modules/rockhopper/download-queue';
import { downloadToDisk } from '../modules/rockhopper/file-cache';

export default connect(
(state, ownProps) => ({
  programKey: selectProgramKey(state),
  checksum: selectCurrentFileChecksum(state),
  progress: selectCurrentFileProgress(state),
  line: selectCurrentLine(state)
}),
(dispatch, ownProps) => ({
  onDownloadProgram: (path, fileType) => {
    downloadQueue.addDownload(path, fileType, downloadToDisk)
  }
}))(GCodeEditor);
