import { connect } from 'react-redux';
import MachineHistory from '../components/MachineHistory';
import { selectSpindleSeconds } from '../modules/rockhopper/selectors';
import actions from '../modules/rockhopper/actions';

export default connect( 
(state, ownProps) => ({
  spindleSeconds: selectSpindleSeconds(state),
}), 
(dispatch, ownProps) => ({ 
  onLsLogs: () => dispatch(actions.get.lsLogs()),
}))(MachineHistory);
