import { connect } from 'react-redux';

import AddIcon from "@material-ui/icons/Add";
import RemoveIcon from "@material-ui/icons/Remove";


import pentaActions from '../modules/pentaui/actions';
import rockhopperActions from '../modules/rockhopper/actions';
import ButtonFlankedDropdown from '../components/ButtonFlankedDropdown';


import { selectAxisSelectJogAxis, selectAxisSelectJogDigitIndex, selectJogIncrement } from '../modules/pentaui/selectors';
import { selectAxes, selectDisplayUnits, selectJogConfig, selectJogMode, selectTrajCoordinates, selectUnitsCode, selectKeyboardShortcutsJogModifiers } from '../modules/rockhopper/selectors';

import { toDisplayUnitsNoContext, getDisplayUnitStringFromTypeNoContext, toMachineUnits } from "../hooks/useUnits";
import { CONTINUOUS_JOG_CODES } from "../modules/pentaui/constants";
import { JOG_INCREMENT_LIMITS, UNITS } from "../modules/rockhopper/constants";

const STEP_SIZE_OPTIONS = (unit) => {
  switch(unit) {
    case 'deg':
      return [10, 1, 0.1, 0.01, 0.001]
    case 'mm':
      return [1, 0.1, 0.01, 0.001]
    case 'in':
      return [0.1, 0.01, 0.001, 0.0001]
    default:
      return []
  }
}

export const mapStateToProps = (state, ownProps) => {
  return { 
    axes: selectAxes(state),
    axisSelectJogAxis: selectAxisSelectJogAxis(state) ? selectAxisSelectJogAxis(state) : 'X',
    axisSelectJogDigitIndex: selectAxisSelectJogDigitIndex(state) ?  selectAxisSelectJogDigitIndex(state) : 0,
    displayUnits: selectDisplayUnits(state),
    customJogIncrement: selectJogIncrement(state),
    jogConfig: selectJogConfig(state),
    jogMode: selectJogMode(state),
    modifierKeys: selectKeyboardShortcutsJogModifiers(state),
    trajCoords: selectTrajCoordinates(state) ? selectTrajCoordinates(state) : 'XYZAB',
    unitsCode: selectUnitsCode(state),
  }
};

export const mapDispatchToProps = (dispatch, ownProps) => ({
  onSetDisplay: (val) => dispatch(pentaActions.jog.setDisplay(val)),
  onJogIncrement: (axis, joint, jogMode, speed, increment ) => {
    const limits = axis > 2 ? JOG_INCREMENT_LIMITS.ANGULAR : JOG_INCREMENT_LIMITS.LINEAR;
    if(Math.abs(increment) < limits.MIN || Math.abs(increment) > limits.MAX){
      dispatch(rockhopperActions.messages.warning(`${increment} machine-units is outside Jog Increment limits. You may have encountered a bug if you are seeing this message`))
      return;
    }
    dispatch(rockhopperActions.putHelpers.jogIncrement({ axis, joint, jogMode, speed, increment }));
  },
  onJogContinuous: (axis, joint, jogMode, speed, ) => {
    dispatch(rockhopperActions.putHelpers.jogContinuous({ axis, joint, jogMode, speed }));
  },
  onJogStop: (axis, joint, jogMode) => {
    dispatch(rockhopperActions.putHelpers.jogStop({ axis, joint, jogMode }));
  },
  onSetOpenDigitIndex: (axis) => {
    dispatch(pentaActions.axisSelectJog.setDigitIndex(axis));
  },
  onSetJogIncrement: (val) => {
    dispatch(pentaActions.jog.setIncrement(val));
  },
  onRejectIncrement: (val) => {
    dispatch(rockhopperActions.messages.warning(`${val} is outside Jog Increment limits`))
  }
});

export const mergeProps = (stateProps, dispatchProps, ownProps) => {
  const {
    jogConfig,
    jogMode,
    modifierKeys,
    customJogIncrement,

    axes,
    axisSelectJogAxis,
    axisSelectJogDigitIndex,
    displayUnits,
    trajCoords,
    unitsCode,

  } = stateProps;

  const {
    onJogIncrement,
    onJogContinuous,
    onJogStop,
    onSetOpenDigitIndex,
    onSetJogIncrement,
    onRejectIncrement,
  } = dispatchProps;

  const jointIndex = trajCoords.indexOf(axisSelectJogAxis);
  const axisIndex = axes && jointIndex ? axes[jointIndex].index : 0;
  const isRotary = jointIndex > 2;
  const unit = isRotary ? getDisplayUnitStringFromTypeNoContext("angle", displayUnits, unitsCode) : getDisplayUnitStringFromTypeNoContext("length", displayUnits, unitsCode);
  const incrementBase = isRotary ? 0.001 : unit === "mm" ? 0.001 : 0.0001;
  const axisSelectIncrement = incrementBase * Math.pow(10, axisSelectJogDigitIndex - 1);
  const machineUnit = isRotary ? "deg" : "in";

  const customJogIncrementDisplayUnits = CONTINUOUS_JOG_CODES.includes(customJogIncrement) ? (
    customJogIncrement
  ):(
    toDisplayUnitsNoContext({value: customJogIncrement, unit: machineUnit}, unit, unitsCode).value 
  );

  const commandIncrement = customJogIncrement ? customJogIncrementDisplayUnits: axisSelectIncrement;
  const displayString = CONTINUOUS_JOG_CODES.includes(customJogIncrement) ? "Continuous" : [parseFloat(commandIncrement.toFixed(5)), unit].join(' ');

  const handleJog = (e, dir) => {
    const {
      shiftKey,
      altKey,
      ctrlKey,
      metaKey,
    } = e;
    const modifiers = { 'Ctrl' : ctrlKey, 'Shift' : shiftKey, 'Alt': altKey, 'Meta': metaKey};
    const slow = modifiers[modifierKeys['slow']];
    const baseSpeed = jointIndex > 2 ? jogConfig.angularSpeed : jogConfig.linearSpeed;
    const speed = slow ? baseSpeed * jogConfig.slowJogFraction : baseSpeed;
    if(CONTINUOUS_JOG_CODES.includes(customJogIncrement))
    {
      onJogContinuous(axisIndex, jointIndex, jogMode, dir * speed);
    }
    else{
      const incMachineUnits = toMachineUnits({value: commandIncrement, unit: unit}).value;
      onJogIncrement( axisIndex, jointIndex, jogMode, dir * speed, incMachineUnits, )
    }
  }
  const handleSetJogIncrement = (val, isSubmit=false) => {
    if(CONTINUOUS_JOG_CODES.includes(val)){
      onSetJogIncrement(0);
      onSetOpenDigitIndex(0);
      return;
    }
    const stringVal = val.toString();
    const inputNumber = stringVal.replace(/[^0-9|.]/g, '')
    const floatVal = parseFloat(inputNumber);
    const inputOther = stringVal.match(/[^0-9|.]/g)
    const unitVal = inputOther ? inputOther.join('').trim() : null;
    //Try to use the entry if it starts with a number
    if( !isNaN(floatVal) ) {
      const displayUnit = isRotary ? displayUnits.angle : displayUnits.length;
      const displayUnitRealUnit = isRotary ? getDisplayUnitStringFromTypeNoContext("angle", displayUnits, unitsCode) : getDisplayUnitStringFromTypeNoContext("length", displayUnits, unitsCode);

      
      //Determine the value of the entry in machine units and in display units
      var machineIncrement;
      var displayIncrement;
      if(unitVal){
        if(UNITS.TYPE_MAP[unitVal] && UNITS.TYPE_MAP[unitVal] === UNITS.TYPE_MAP[displayUnit] ){
          machineIncrement = toMachineUnits({value: floatVal, unit: unitVal}, unitsCode).value
          displayIncrement = toDisplayUnitsNoContext({value: floatVal, unit: unitVal}, displayUnit, unitsCode).value
        }
        else{
          //If the unit is unknown, do nothing
          return false;
        }
      }
      else{
        //If only a single word is present, assume the value is in displayUnits
        displayIncrement = floatVal;
        machineIncrement = toMachineUnits({value: floatVal, unit: displayUnit}, unitsCode).value;
      }

      const limits = isRotary ? JOG_INCREMENT_LIMITS.ANGULAR : JOG_INCREMENT_LIMITS.LINEAR;
      if( !(machineIncrement < limits.MIN || machineIncrement > limits.MAX) ){
        const inc = parseFloat(machineIncrement.toFixed(8));
        onSetJogIncrement(inc);
        //Update axisSelect
        //If the value is not among the power-of-10 options for the axis/displayUnit combination,
        //the openDigitIndex becomes -1, which will result in no highlighted value in the jogSpinners
        const optionsIndex = STEP_SIZE_OPTIONS(displayUnitRealUnit).reverse().indexOf(displayIncrement);
        onSetOpenDigitIndex(optionsIndex + 1);
      }
      else{
        if(isSubmit){
          onRejectIncrement(val)
        }
      }
    }
  };
  const handleSelectJogIncrement = (index, value) => {
    //The axisSelectJogDigitIndex starts at 1 for the position of the smallest joggable digit
    if(value === "Continuous"){
      onSetOpenDigitIndex(0);
    }
    else{
      onSetOpenDigitIndex(index + 1);
    }
    handleSetJogIncrement(value);
  };
  const handleSubmitJogIncrement = (value) => {
      handleSetJogIncrement(value, true);
  }

  const handleLeftDown = (e) => {
    handleJog(e, -1)
  }

  const handleRightDown = (e) => {
    handleJog(e, 1);
  }

  const handleUp = () => {
    if(CONTINUOUS_JOG_CODES.includes(customJogIncrement)){
      onJogStop(axisIndex, jointIndex, jogMode)
    }
  }

  return{
    ...ownProps,
    value: displayString,
    label: unit,
    dropdownOptions: STEP_SIZE_OPTIONS(unit).reverse().concat("Continuous"),
    leftBtnDom:<RemoveIcon/>,
    rightBtnDom:<AddIcon/>,
    leftBtnProps: {
      onPointerDown: handleLeftDown,
      onPointerUp: handleUp,
      onPointerLeave: handleUp,
    },
    rightBtnProps: {
      onPointerDown: handleRightDown,
      onPointerUp: handleUp,
      onPointerLeave: handleUp,
    },
    onChange: handleSetJogIncrement,
    onSelect: handleSelectJogIncrement,
    onSubmit: handleSubmitJogIncrement,
  }
};

export default connect( mapStateToProps, mapDispatchToProps, mergeProps )(ButtonFlankedDropdown);
