import { connect } from 'react-redux';
import KeyboardShortcuts from '../components/KeyboardShortcuts';
import actions from '../modules/rockhopper/actions';
import pentaActions from '../modules/pentaui/actions';
import { DEFAULT_TAB, MANUAL_TAB, SETUP_TAB } from '../components/Tabs';
import { selectLocation } from '../modules/router/selectors';
import { selectEnableKeyboardJogging, selectAxisSelectJogDigitIndex, selectAxisSelectJogAxis, selectJogIncrement } from '../modules/pentaui/selectors';
import { selectAxes,
         selectMaxLinearVelocity,
         selectMachineStatus,
         selectDelayedRapidRatePercentage,
         selectDelayedMaxVelPercentage,
         selectDelayedFeedratePercentage,
         selectDelayedSpindle,
         selectTrajCoordinates,
         selectKeyboardShortcutsMap,
         selectPositions,
         selectUnitsCode,
         selectDisplayUnits,
         selectReadyForMdi,
         selectMdiHistory,
         selectJogMode,
         selectJogConfig} from '../modules/rockhopper/selectors';
import { toMachineUnits } from '../hooks/useUnits';
import { CONTINUOUS_JOG_CODES } from '../modules/pentaui/constants';
import { JOG_INCREMENT_LIMITS } from '../modules/rockhopper/constants';

export const stringToActionMap = {
  'estopDisable': () => actions.put.estopCmd({data:[false]}),
  'estopEnable': () => actions.put.estopCmd({data:[true]}),
  'feedrateDecrement1': () => actions.put.incrementFeedrateCmd({data: [-0.01]}),
  'feedrateIncrement1': () => actions.put.incrementFeedrateCmd({data: [+0.01]}),
  'feedrateDecrement10': () => actions.put.incrementFeedrateCmd({data: [-0.1]}),
  'feedrateIncrement10': () => actions.put.incrementFeedrateCmd({data: [+0.1]}),
  'spindlerateDecrement1': () => actions.put.incrementSpindleOverrideCmd({data: [-0.01]}),
  'spindlerateIncrement1': () => actions.put.incrementSpindleOverrideCmd({data: [+0.01]}),
  'spindlerateDecrement10': () => actions.put.incrementSpindleOverrideCmd({data: [-0.1]}),
  'spindlerateIncrement10': () => actions.put.incrementSpindleOverrideCmd({data: [+0.1]}),
  'homeAll': () => actions.put.homeCmd(),
  'feedHold': () => actions.put.feedHoldCmd(),
  'step': () => actions.put.autoCmd({ data: [ "AUTO_STEP" ] }),
  'cycleStart': () => actions.put.cycleStartCmd(),
  'reset': () => actions.put.abortCmd(),
};

export const stringToAxisSelectJogMap = {
  'axisSelectJogSelectX': () => pentaActions.axisSelectJog.setAxis('X'),
  'axisSelectJogSelectY': () => pentaActions.axisSelectJog.setAxis('Y'),
  'axisSelectJogSelectZ': () => pentaActions.axisSelectJog.setAxis('Z'),
  'axisSelectJogSelectA': () => pentaActions.axisSelectJog.setAxis('A'),
  'axisSelectJogSelectB': () => pentaActions.axisSelectJog.setAxis('B'),
  'axisSelectJogSelectC': () => pentaActions.axisSelectJog.setAxis('C'),
  'axisSelectJogIncreaseStepSize': () => pentaActions.axisSelectJog.incrementDigitIndex(1),
  'axisSelectJogDecreaseStepSize': () => pentaActions.axisSelectJog.incrementDigitIndex(-1),
  'axisSelectJogNextAxis': (axisSelectJogAxis, trajCoords) => pentaActions.axisSelectJog.incrementAxis({inc: 1, axisSelectJogAxis, trajCoords}),
  'axisSelectJogPreviousAxis': (axisSelectJogAxis, trajCoords) => pentaActions.axisSelectJog.incrementAxis({inc: -1, axisSelectJogAxis, trajCoords}),
};

const isLocationJoggable = (location) => {
  if(location.startsWith(MANUAL_TAB) || location.startsWith(SETUP_TAB)){
    return true
  }
  else if(location === "/" && (DEFAULT_TAB === MANUAL_TAB || DEFAULT_TAB === SETUP_TAB)){
    return true
  }
};

export const mapStateToProps = (state, ownProps) => {
  return {
    axes: selectAxes(state),
    axisSelectJogAxis: selectAxisSelectJogAxis(state),
    axisSelectJogDigitIndex: selectAxisSelectJogDigitIndex(state),
    displayUnits: selectDisplayUnits(state),
    enable: true,
    enableKeyboardJogging: selectEnableKeyboardJogging(state),
    feedrate: selectDelayedFeedratePercentage(state),
    jogConfig: selectJogConfig(state),
    customJogIncrement: selectJogIncrement(state),
    jogMode: selectJogMode(state),
    location: selectLocation(state).pathname,
    machineStatus: selectMachineStatus(state),
    maxLinearVelocity: selectMaxLinearVelocity(state),
    maxRapid: selectDelayedRapidRatePercentage(state),
    maxVel: selectDelayedMaxVelPercentage(state),
    mdiHistory: selectMdiHistory(state),
    positions: selectPositions(state),
    readyForMdi: selectReadyForMdi(state),
    shortcutsMap: selectKeyboardShortcutsMap(state),
    spindle: selectDelayedSpindle(state),
    trajCoords: selectTrajCoordinates(state),
    unitsCode: selectUnitsCode(state),
  }
};

export const mapDispatchToProps = (dispatch, ownProps) => ({
  // These will fire when keyboard shortcuts are available or not.
  // We should probably dispatch actions to make this information
  // available in the UI with some kind of indicator (in the old
  // UI there was a blue or orange button in the bottom bar).
  onFocus: () => {
  //    console.log("keyboard shortcuts focused, can receive key events")
  },
  onBlur: () => {
  //    console.log("keyboard shortcuts blurred, cannot receive key events")
  },
  onJogContinuous: (axis, joint, speed, jogMode) => {
    dispatch(actions.putHelpers.jogContinuous({ axis, joint, speed, jogMode }));
  },
  onJogIncrement: (axis, joint, speed, increment, jogMode) => {
    const limits = axis > 2 ? JOG_INCREMENT_LIMITS.ANGULAR : JOG_INCREMENT_LIMITS.LINEAR;
    if(Math.abs(increment) < limits.MIN || Math.abs(increment) > limits.MAX){
      dispatch(actions.messages.warning(`${increment} machine-units is outside Jog Increment limits. You may have encountered a bug if you are seeing this message`))
      return;
    }
    dispatch(actions.putHelpers.jogIncrement({ axis, joint, speed, increment, jogMode }));
  },
  onJogStop: (axis, joint, jogMode) => {
    dispatch(actions.putHelpers.jogStop({ axis, joint, jogMode }));
  },
  onAction: dispatch,
  onSendMdiCmd: (cmd, history) => {
    dispatch(actions.put.mdiCmd({ data: [ cmd ] }));
    const newHistoryItem = {cmd: cmd, time: Date.now()};
    const newHistory = history ? history.concat(newHistoryItem) : [newHistoryItem]
    dispatch( actions.put.saveClientConfig({ data: [ 'MDIHistory', newHistory ], }));
  },
  onRejectMdiCmd: (cmd, reason) => {
    dispatch(actions.messages.warning(`Cannot issue MDI command "${cmd}": ` + reason))
  },
  onRejectJogCmd: (warning) => {
    dispatch(actions.messages.warning(warning))
  },
});

export const mergeProps = (stateProps, dispatchProps, ownProps) => {
  const {
    axes,
    axisSelectJogAxis,
    axisSelectJogDigitIndex,
    customJogIncrement,
    displayUnits,
    enable,
    enableKeyboardJogging,
    jogConfig,
    jogMode,
    location,
    mdiHistory,
    readyForMdi,
    shortcutsMap,
    trajCoords,
    unitsCode,
  } = stateProps;
  const {
    onFocus,
    onBlur,

    onJogContinuous,
    onJogIncrement,
    onJogStop,

    onAction,
    onSendMdiCmd,
    onRejectMdiCmd,
    onRejectJogCmd,
  } = dispatchProps;
  return {
    enable,
    onFocus,
    onBlur,
    onKeyDown: (e, playTick) => {
      const {
        nativeEvent,
        shiftKey,
        altKey,
        ctrlKey,
        metaKey,
        repeat,
      } = e;
      const modifierString = (ctrlKey ? 'Ctrl+' : '') + (shiftKey ? 'Shift+' : '') + (altKey ? 'Alt+' : '') + (metaKey ? 'Meta+' : '');
      let code = nativeEvent.code;
      if(code.startsWith('Digit')){
        code = code.slice(5);
      }
      else if(code.startsWith('Key')){
        code = code.slice(3);
      }
      const keyCombination = modifierString + code;
      if(!repeat && shortcutsMap.hasOwnProperty(keyCombination)){
        const cmd = shortcutsMap[keyCombination];
        if(cmd.startsWith('jog')){ //jog commands are strings with form: jog{axis}{direction}{optional modifier, either slow or incremental}.
          if( isLocationJoggable(location) ) {
            if(enableKeyboardJogging) {
              const jointIndex = trajCoords.indexOf(cmd[3]); //The 4th character is an uppercase letter representing an axis: X, Y, Z, A, B, or C
              if(jointIndex !== -1){
                const direction = cmd[4] === '+' ? +1 : -1; //The 5th character represents direction: + or -
                const axisData = axes[jointIndex];
                const baseSpeed = jointIndex > 2 ? jogConfig.angularSpeed : jogConfig.linearSpeed;
                const speed = cmd.includes('slow') ? baseSpeed * jogConfig.slowJogFraction : baseSpeed;
                if(cmd.includes('incremental')){
                  let commandIncrement;
                  if(CONTINUOUS_JOG_CODES.includes(customJogIncrement)){
                    onRejectJogCmd("Cannot increment jog while custom increment is CONTINUOUS");
                    //can't increment jog while increment is set to continuous
                    return;
                  }
                  else if (customJogIncrement){
                    commandIncrement = customJogIncrement;
                  }
                  else{
                    const displayUnit = jointIndex > 2 ? displayUnits.angle : displayUnits.length;
                    const incrementBase = jointIndex > 2 ? 0.001 : displayUnit === 'mm' || (displayUnit === 'pu' && unitsCode === 'G21') ? 0.001 : 0.0001;
                    const power = jointIndex > 2 ? axisSelectJogDigitIndex - 1 : Math.min(3, axisSelectJogDigitIndex - 1);
                    const increment = incrementBase * Math.pow(10, power);
                    commandIncrement = toMachineUnits({value: increment, unit: displayUnit}, unitsCode).value;
                  }
                  const limits = jointIndex > 2 ? JOG_INCREMENT_LIMITS.ANGULAR : JOG_INCREMENT_LIMITS.LINEAR;
                  commandIncrement = Math.max(limits.MIN, Math.min(limits.MAX, commandIncrement));
                  onJogIncrement(axisData.index, jointIndex, direction * speed, commandIncrement, jogMode);
                }
                else if(cmd.includes('slow')){
                  onJogContinuous(axisData.index, jointIndex, speed * direction, jogMode);
                }
                else{
                  onJogContinuous(axisData.index, jointIndex, speed * direction, jogMode);
                }
                playTick();
              }
            }
          }
        }
        else if(cmd.startsWith('axisSelectJog')){
          if(stringToAxisSelectJogMap.hasOwnProperty(cmd)){
            const action = stringToAxisSelectJogMap[cmd](axisSelectJogAxis, trajCoords);
            onAction(action);
            playTick();
          }
          else{
            if( isLocationJoggable(location) ) {
              if(enableKeyboardJogging) {
                const jointIndex = trajCoords.indexOf(axisSelectJogAxis);
                if(jointIndex !== -1 && axisSelectJogDigitIndex >= 0 && axisSelectJogDigitIndex <= 5){
                  const axisData = axes[jointIndex]
                  const direction = cmd.includes('+') ? +1 : -1; //The 5th character represents direction: + or -
                  const baseSpeed = jointIndex > 2 ? jogConfig.angularSpeed : jogConfig.linearSpeed;
                  const speed = cmd.includes("slow") ? baseSpeed * jogConfig.slowJogFraction : baseSpeed;
                  if(CONTINUOUS_JOG_CODES.includes(customJogIncrement)){
                    onJogContinuous(axisData.index, jointIndex, speed * direction, jogMode);
                  }
                  else{
                    let commandIncrement;
                    if(customJogIncrement){
                      commandIncrement = customJogIncrement;
                    }
                    else{
                      const displayUnit = jointIndex > 2 ? displayUnits.angle : displayUnits.length;
                      const incrementBase = jointIndex > 2 ? 0.001 : displayUnit === 'mm' || (displayUnit === 'pu' && unitsCode === 'G21') ? 0.001 : 0.0001;
                      const power = jointIndex > 2 ? axisSelectJogDigitIndex - 1 : Math.min(4, axisSelectJogDigitIndex - 1);
                      const increment = incrementBase * Math.pow(10, power);
                      commandIncrement = toMachineUnits({value: increment, unit: displayUnit}, unitsCode).value;
                    }
                    const limits = jointIndex > 2 ? JOG_INCREMENT_LIMITS.ANGULAR : JOG_INCREMENT_LIMITS.LINEAR;
                    commandIncrement = Math.max(limits.MIN, Math.min(limits.MAX, commandIncrement));
                    onJogIncrement(axisData.index, jointIndex, direction * speed, commandIncrement, jogMode);
                    playTick();
                  }
                }
              }
            }
          }
        }
        else if(cmd.startsWith('mdi')){
          if(location.startsWith(MANUAL_TAB) || location.startsWith(SETUP_TAB)) {
            if(readyForMdi.isReady){
              onSendMdiCmd(cmd.slice(3), mdiHistory);
            }
            else{
              onRejectMdiCmd(cmd.slice(3), readyForMdi.reason)
              // dispatch(actions.messages.warning(cmd, readyForMdi.reason))
            }
          }
        }
        else{          const action = stringToActionMap[cmd]();
          onAction(action)
          playTick();
        }
      }
    },
    onKeyUp: (e) => {
      const {
        nativeEvent,
        shiftKey,
        altKey,
        ctrlKey,
        metaKey,
        // repeat,
      } = e;
      const modifierString = (ctrlKey ? 'Ctrl+' : '') + (shiftKey ? 'Shift+' : '') + (altKey ? 'Alt+' : '') + (metaKey ? 'Meta+' : '');
      let code = nativeEvent.code;
      if(code.startsWith('Digit')){
        code = code.slice(5);
      }
      else if(e.nativeEvent.code.startsWith('Key')){
        code = code.slice(3);
      }
      const keyCombination = modifierString + code;
      if(shortcutsMap.hasOwnProperty(keyCombination)){
        const actionKey = shortcutsMap[keyCombination];
        if(actionKey.startsWith('jog') && !actionKey.includes("incremental")){
          const actionKey = shortcutsMap[code];
          const jointIndex = trajCoords.indexOf(actionKey[3]);
          if(jointIndex !== -1){ 
            //don't jog an axis that doesn't exist - default keybindings don't account for machine type, and include binds for XYZABC
            const axisData = axes[jointIndex]
            onJogStop(axisData.index, jointIndex, jogMode)
          }
        }
        else if(shortcutsMap[code].startsWith('axisSelectJogJog') && CONTINUOUS_JOG_CODES.includes(customJogIncrement)){
          const jointIndex = trajCoords.indexOf(axisSelectJogAxis);
          if(jointIndex !== -1){
            const axisData = axes[jointIndex]
            onJogStop(axisData.index, jointIndex, jogMode)
          }
        }
      }
    }
  };
};

export default connect(mapStateToProps, mapDispatchToProps, mergeProps)(KeyboardShortcuts);
