import { connect } from 'react-redux';

import { selectIsLogsBundleInProgress } from '../modules/rockhopper/selectors';
import actions from '../modules/rockhopper/actions';

import DownloadLogsButton from 'components/DownloadLogsButton';

export default connect( 
(state, ownProps) => ({
  isLogsBundleInProgress: selectIsLogsBundleInProgress(state),
}),
(dispatch, ownProps) => ({ 
  onDownloadLogs: () => {
    dispatch(actions.fileDownload.downloadLogsBundle());
  },
}))(DownloadLogsButton);
