export const selectConnected = (state) => state.calib.connected;
export const selectWebSocket = (state) => state.calib.webSocket;

export const selectCmmConnected = (state) => state.calib.calibStatus.status.cmmConnected;
export const selectSelectedProcess = (state) => state.calib.calibStatus.selectedProcess;
export const selectServiceStatus = (state) => {
  return state.calib.serviceStatus;
}
export const selectCalibStatus = (state) => state.calib.calibStatus;
export const selectCalibBegun = (state) => state.calib.calibStatus && state.calib.calibStatus.begun;

export const selectCalibPercentage = (state) => {
  var stagesComplete = 0;
  var totalStages = 0;
  if(state.calib.calibStatus && state.calib.calibStatus.stages){
    if(state.calib.calibStatus.stages.calib){
      totalStages += Object.keys(state.calib.calibStatus.stages.calib).length;
      Object.keys(state.calib.calibStatus.stages.calib).forEach((stage) => {
        var completed = state.calib.calibStatus.stages.calib[stage].completed;
        stagesComplete = stagesComplete + (completed ? 1 : 0);
      })
    }
    if(state.calib.calibStatus.stages.verify){
      totalStages += Object.keys(state.calib.calibStatus.stages.verify).length;
      Object.keys(state.calib.calibStatus.stages.verify).forEach((stage) => {
        var completed = state.calib.calibStatus.stages.verify[stage].completed;
        stagesComplete = stagesComplete + (completed ? 1 : 0);
      })
    }
  }
  var percentageComplete = stagesComplete / totalStages * 100;
  return percentageComplete;
}


