import { useRef, useCallback } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Box from '@material-ui/core/Box';
import Button from '@material-ui/core/Button';
import Textfield from '@material-ui/core/Textfield';
import LinearProgress from '@material-ui/core/LinearProgress';
import CircularProgress from '@material-ui/core/CircularProgress';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';

import ConnectedIcon from '@material-ui/icons/RadioButtonUnchecked';
import DisconnectedIcon from '@material-ui/icons/Block';

import { STATE_PAUSE, STATE_IDLE, STATE_COMPLETE } from '../modules/calib/constants';
import { Typography } from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
  serial: {
    marginRight: theme.spacing(1)
  },
  machine: {
    marginRight: theme.spacing(1),
    width: "80px"
  },
  calibrationTab: {
    width: "100%",
    minHeight: "200px",
    maxHeight: "500px",
    overflow: "scroll"
  },
  calibrationContent: {
    height: "200px",
    verticalAlign: "middle",
    display: "flex",
    flexDirection: "column",
    justifyContent: "center"
  },
  startButton: {
    marginLeft: theme.spacing(1),
    verticalAlign: "center"
  },
  stopButton: {
    marginLeft: theme.spacing(1)
  },
  icon: {
    verticalAlign: "middle"
  },
  connected: {
    color: theme.palette.primary.main
  },
  disconnected: {
    color: theme.palette.secondary.main
  }
}));

function CalibSimple(props) {
  const {
    serviceConnected,
    cmmConnected,
    calibStatus,
    calibPercentage,

    onUploadFiles,
    onStartCalibration,
    onStopCalibration,
    onPauseCalibration,
    onResumeCalibration,

    value,
    visible,
    index
  } = props;

  const serialRef = useRef(null);
  const variantRef = useRef(null);

  const failedStages = [];
  Object.keys((calibStatus.spec ? calibStatus.spec : {})).forEach(stageName => {
    if(calibStatus.spec[stageName].pass === false){
      failedStages.push([stageName,calibStatus.spec[stageName].val].join(' '))
    }
  })
  const specLabel = failedStages.length > 0 ? "Out of Spec: " + failedStages.join(' ,') : null;
  const errorLabel = calibStatus.status.error ? "Error: " + calibStatus.status.errorMsg : null
  const statusLabel = calibStatus.status ? "Stage: " + calibStatus.status.currentStage : 'Not connected';

  const paused =  [STATE_PAUSE, STATE_IDLE].includes(calibStatus.processState);
  const completed = [STATE_COMPLETE].includes(calibStatus.processState);

  const classes = useStyles();

  const handleStartCalibration = useCallback(() => {
    if(serialRef && serialRef.current) {
      console.log("serialRef.current.value", serialRef.current.value);
      console.log("variantRef.current.value", variantRef.current.value);
      if(onStartCalibration) {
        onStartCalibration(serialRef.current.value, variantRef.current.value);
      }
    }
  }, [ onStartCalibration ]);
  const handleUploadFiles = useCallback(() => {
    if(serialRef && serialRef.current) {
      console.log("serialRef.current.value", serialRef.current.value);
      console.log("variantRef.current.value", variantRef.current.value);
      if(onUploadFiles) {
        onUploadFiles(serialRef.current.value, variantRef.current.value);
      }
    }
  }, [ onUploadFiles ]);
  const handleStopCalibration = useCallback(() => {
    if(onStopCalibration) {
      onStopCalibration();
    }
  }, [ onStopCalibration ]);

  const handlePauseCalibration = useCallback(() => {
    if(onPauseCalibration) {
      onPauseCalibration();
    }
  }, [ onPauseCalibration ]);

  const handleResumeCalibration = useCallback(() => {
    if(onResumeCalibration) {
      onResumeCalibration();
    }
  }, [ onResumeCalibration ]);

  return <div
    role="tabpanel"
    hidden={value !== index}
    id={`simple-tabpanel-${index}`}
    aria-labelledby={`simple-tab-${index}`}
  >
    { visible &&
      <div className={classes.calibrationTab}>
      <Box display="flex">
        <Box m="auto">
          <Box m={2} className={classes.calibrationContent}>
            <Box display="flex">
            {
              serviceConnected ? 
              <Box sx={{ width: "500px", maxWidth: "80vw" }}>
                <Box mb={1}>
                  {}
                  {specLabel && <Typography style={{color: "orange"}}>{specLabel}</Typography>}
                  {errorLabel && <Typography style={{color: "red"}}>{errorLabel}</Typography>}
                  <div>
                    { statusLabel } { paused ? null : <CircularProgress variant="indeterminate" size={15}/> }
                  </div>
                </Box>
                <LinearProgress variant="determinate" value={calibPercentage}/>
                <Box mt={1}>{ paused ? <Button variant="contained" color="primary" onClick={handleResumeCalibration}>Resume</Button> : <Button variant="contained" color="primary" onClick={handlePauseCalibration}>Pause</Button> }<Button variant="contained" color="secondary" classes={{ root: classes.stopButton }} onClick={handleStopCalibration}>Stop</Button></Box>
                <Box mt={1}>{ cmmConnected ? <span className={classes.connected}><ConnectedIcon classes={{ root: classes.icon }}/> CMM Connected</span> : <span className={classes.disconnected}><DisconnectedIcon classes={{ root: classes.icon }}/> CMM Disconnected</span>}</Box>
                <Box mt={1}>{ completed && <span className={classes.connected}>Calibration Complete</span>}</Box>
              </Box>
               :
              <>
                <Textfield variant="outlined" placeholder="Enter serial number" inputRef={serialRef} classes={{ root: classes.serial }}/>
                <FormControl>
                <InputLabel variant="outlined" id="demo-simple-select-label">Machine</InputLabel>
                <Select defaultValue={""} labelId="demo-simple-select-label" labelWidth={60} variant="outlined" inputProps={{ ref: variantRef }} classes={{ root: classes.machine }}>
                  <MenuItem value="50">V2-50</MenuItem>
                  <MenuItem value="10">V2-10</MenuItem>
                </Select>
                </FormControl>
                {/* <Button variant="contained" disabled={serialRef.current && serialRef.current.value.length < 1} color="primary" classes={{ root: classes.startButton }} onClick={handleStartCalibration}>Start Calibration</Button> */}
                <Button variant="contained" color="primary" classes={{ root: classes.startButton }} onClick={handleStartCalibration}>Start Calibration</Button>
                <Button variant="contained" color="secondary" classes={{ root: classes.startButton }} onClick={handleUploadFiles}>Upload Files</Button>
              </>
            }
            </Box>
            <div><Box mt={1}>{ completed && <span className={classes.connected}>Calibration Complete</span>}</Box></div>
          </Box>
        </Box>
      </Box>
      </div>
    }
  </div>
}

export default CalibSimple;
