import React from 'react';

import MenuOpenIcon from '@material-ui/icons/MenuOpen';

import { makeStyles } from '@material-ui/styles';

import useTranslation from '../hooks/useTranslation';
import useUnits from '../hooks/useUnits';

import ModalSubmittableTextField from './ModalSubmittableTextField';

const useStyles = makeStyles((theme) => ({
  offsetItemContainer: {
    borderColor: theme.palette.primary.main,
    borderRadius: 5,
    borderStyle: 'solid',
    borderWidth: 2,
    height: 38
  },
  overrideTextFieldInputInput: {
    ...theme.typography.dataText,
  },
  root: {
    height: "100%",
    width: "100%",
  },
}));

const WorkOffsetsItem = (props) => {
  const {
    itemName,
    axisData, 
    decimals,
    modalTitle,
    offset, 
    disabled,

    onSetOffset,
    onSetDroViaOffset,
  } = props;

  const { t } = useTranslation();
  const { toDisplayUnits } = useUnits();
  const classes = useStyles();

  return <ModalSubmittableTextField
    value={toDisplayUnits(offset).value.toFixed(decimals)}
    modalValue={"0.0000"}
    onSubmitTextField={(value) =>  onSetOffset(value, axisData)}
    onSubmitModal={(value) => onSetDroViaOffset(value, axisData)}
    disabled={disabled}
    buttonDom={
      <>
        <div style={{fontSize: 6}}>DRO</div>
        <MenuOpenIcon />
      </>
    }
    buttonProps={{
      color: "primary",
      "aria-label": `open ${axisData.axis}-axis set-dro-via-${itemName} modal`
    }}
    containerProps={{
      className: classes.offsetItemContainer
    }}
    modalProps={{
      title: modalTitle,// `${t('set')} ${(axisData.axis).toUpperCase()} ${t('dro')} ${t('via')} ${g5xLabel(g5xIndex)}`,
      textFieldLabel: t('newDroValue'),
      submitBtnLabel: t('set'),
      cancelBtnLabel: t('cancel'),
      dialogProps: {
        "aria-label": `${axisData.axis}-axis set-dro-via-${itemName} modal`
      }
    }}
    textFieldProps={{
      inputProps: {
          "aria-label" : `${axisData.axis}-axis set-${itemName} textbox`,
      },
      InputProps: {
        disableUnderline: true,
        classes: {
          input: classes.overrideTextFieldInputInput,
          root: classes.root,
        }
      }
    }}
  />;
};
export default WorkOffsetsItem;
