import { MODULE_ID } from './constants';
import reducer from './reducers';
import { alwaysReconnect } from './sagas';

const calib = () => {
  // Not in a context that has access to useLocation, so use window.location.
  // Since we use hash routing, useLocation can provide a parsed query string from the hash portion
  // of the URL. This differs from the query string in the URL. The query string normally
  // is before the hash portion and is part of the URL that is sent to the server. Everything after
  // the hash is client-side only and is what we use for routing in the UI, as well as a client-side
  // query string for hiding/showing certain features.
  // See MachineConfig.js or ClientTab.js for examples of how we're using query strings in the
  // hash fragment to control whether internal or advanced features are shown in the UI.

  // So, we use window.location.hash, rather than window.location.search here.
  // We don't want clients in the field to be wasting their effort looking for the calibration server
  // that will never be there, so only use the alwaysReconnect saga if we have calibration=1 in the
  // hash portion of the URL.
  const sagas = window.location.hash.indexOf("calibration=1") !== -1 ? [ alwaysReconnect ] : [];
  return ({
    id: MODULE_ID,
    reducerMap: {
      calib: reducer
    },
    sagas
  });
}
export default calib;
