import { connect } from 'react-redux';
import Switch from '@material-ui/core/Switch';
import { selectOptionalStop } from '../modules/rockhopper/selectors';
import actions from '../modules/rockhopper/actions';

export default connect( 
(state, ownProps) => ({
  optionalStop: selectOptionalStop(state),
}), 
(dispatch, ownProps) => ({ 
  onEnable: () => {
    dispatch(actions.put.setOptionalStop({ data: [true] }));
  },
  onDisable: () => {
    dispatch(actions.put.setOptionalStop({ data: [false] }));
  },
}),
(stateProps, dispatchProps, ownProps) => {
  const {
    optionalStop,
  } = stateProps;

  const {
    onEnable,
    onDisable,
  } = dispatchProps;

  const onChange = (e) => {
    if(e.target.checked) {
      onEnable();
    } else {
      onDisable();
    }
  };

  //If the initial value of checked is 'undefined' the switch won't respond to changes in optionalStop
  const checked = optionalStop === undefined ? false : optionalStop;

  return {
    ...ownProps,
    checked,
    onChange
  }
}
)(Switch);
