import { connect } from 'react-redux';

import downloadQueue from '../modules/rockhopper/download-queue';
import { downloadToDisk } from '../modules/rockhopper/file-cache';
import { selectLsLogs } from '../modules/rockhopper/selectors';
import actions from '../modules/rockhopper/actions';
import { FILE_TYPES } from 'modules/rockhopper/constants';

import DownloadLogsList from 'components/DownloadLogsList';


export default connect( 
(state, ownProps) => ({
  lsLogs: selectLsLogs(state),
}),
(dispatch, ownProps) => ({
  onDownloadLogFile: (filename) => {
    downloadQueue.addDownload(filename, FILE_TYPES.LOG, downloadToDisk);
  },
  onDownloadLogs: () => {
    dispatch(actions.fileDownload.downloadLogsBundle());
  },
}))(DownloadLogsList);
